import React, { useState, useEffect } from 'react';
import {
  AppBar,
  Toolbar,
  Typography,
  Button,
  Box,
  IconButton,
  Drawer,
  List,
  ListItem,
  useMediaQuery,
  useTheme
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import { Link, useLocation } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';

const Navbar = () => {
  const [mobileOpen, setMobileOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('lg'));
  const location = useLocation();

  const navTextColor = '#e5e7eb';
  const navHoverColor = '#a855f7';
  const navDotColor = '#a855f7';
  const navUnderlineColor = '#a855f7';

  // Handle scroll effect
  useEffect(() => {
    const handleScroll = () => {
      const isScrolled = window.scrollY > 50;
      setScrolled(isScrolled);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const menuItems = [
    { text: 'Home', path: '/' },
    { text: 'About', path: '/about' },
    { text: 'Services', path: '/services' },
    { text: 'Projects', path: '/projects' },
    { text: 'Empanelment', path: '/empanelment' },
    { text: 'Testimonials', path: '/testimonials' },
    { text: 'Contact', path: '/contact' }
  ];

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const drawer = (
    <Box 
      sx={{ 
        textAlign: 'center',
        height: '100%',
        background: 'linear-gradient(135deg, #4f46e5 0%, #7c3aed 50%, #ec4899 100%)',
        color: 'white'
      }}
    >
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', p: 2, borderBottom: '1px solid rgba(255,255,255,0.1)' }}>
        <Typography variant="h6" sx={{ fontWeight: 'bold', letterSpacing: '0.05em' }}>
          Cognix AI
        </Typography>
        <IconButton color="inherit" onClick={handleDrawerToggle}>
          <CloseIcon />
        </IconButton>
      </Box>
      <List sx={{ pt: 2 }}>
        {menuItems.map((item, index) => (
          <motion.div
            key={item.text}
            initial={{ opacity: 0, x: 50 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ delay: index * 0.1, duration: 0.3 }}
            whileHover={{ scale: 1.05, color: '#fde047' }}
            whileTap={{ scale: 0.95 }}
          >
            <ListItem 
              disablePadding
              sx={{ px: 2, mb: 1 }}
            >
              <Button
                component={Link}
                to={item.path}
                onClick={handleDrawerToggle}
                fullWidth
                sx={{
                  color: 'white',
                  fontWeight: location.pathname === item.path ? 'bold' : 'medium',
                  backgroundColor: location.pathname === item.path ? 'rgba(253, 224, 71, 0.2)' : 'transparent',
                  '&:hover': {
                    backgroundColor: 'rgba(253, 224, 71, 0.1)',
                    color: '#fde047',
                    transform: 'scale(1.05)'
                  },
                  py: 1.5,
                  px: 3,
                  fontSize: '1.125rem',
                  textTransform: 'none',
                  borderRadius: 2,
                  justifyContent: 'flex-start',
                  transition: 'all 0.3s ease-in-out'
                }}
              >
                {item.text}
              </Button>
            </ListItem>
          </motion.div>
        ))}
      </List>
    </Box>
  );

  return (
    <>
      <motion.div
        initial={{ y: -100 }}
        animate={{ y: 0 }}
        transition={{ duration: 0.6, ease: "easeOut" }}
      >
        <AppBar 
          position="fixed" 
          elevation={0}
          sx={{ 
            background: 'linear-gradient(135deg, #0b0b0f 0%, #14141a 50%, #0b0b10 100%)',
            backdropFilter: 'none',
            WebkitBackdropFilter: 'none',
            transition: 'all 0.4s cubic-bezier(0.4, 0, 0.2, 1)',
            color: '#ffffff',
            borderBottom: '1px solid rgba(139, 92, 246, 0.2)',
            boxShadow: '0 10px 30px rgba(0, 0, 0, 0.35), inset 0 1px 0 rgba(255, 255, 255, 0.04)',
            zIndex: 50,
            position: 'relative',
            overflow: 'visible',
            '&::before': {
              content: '""',
              position: 'absolute',
              top: 0,
              left: '-100%',
              right: '-100%',
              height: '2px',
              background: 'linear-gradient(90deg, transparent, rgba(139, 92, 246, 0.6), transparent)',
              animation: 'shimmer 10s linear infinite',
              '@keyframes shimmer': {
                '0%': { transform: 'translateX(-100%)' },
                '100%': { transform: 'translateX(100%)' }
              }
            },
            '&::after': {
              content: '""',
              position: 'absolute',
              inset: 0,
              background: 'radial-gradient(circle at 20% 50%, rgba(139, 92, 246, 0.03) 0%, transparent 60%), radial-gradient(circle at 80% 50%, rgba(99, 102, 241, 0.03) 0%, transparent 60%)',
              pointerEvents: 'none'
            }
          }}
        >
          <Toolbar sx={{ minHeight: { xs: '64px', sm: '72px', md: '80px' }, px: { xs: 2.5, sm: 4, md: 6 }, py: { xs: 1.25, sm: 1.5 }, alignItems: 'center', position: 'relative', zIndex: 1 }}>
            <motion.div
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              style={{ flexGrow: 1 }}
            >
              <Link to="/" style={{ textDecoration: 'none' }}>
                <h1 style={{
                  fontSize: 'clamp(1.875rem, 2.5vw, 2.25rem)',
                  fontWeight: 800,
                  letterSpacing: '0.025em',
                  color: '#ffffff',
                  margin: 0,
                  lineHeight: 1
                }}>
                  Cognix{" "}
                  <span style={{
                    background: 'linear-gradient(to right, #c084fc, #3b82f6)',
                    WebkitBackgroundClip: 'text',
                    WebkitTextFillColor: 'transparent',
                    backgroundClip: 'text'
                  }}>
                    AI
                  </span>
                </h1>
              </Link>
            </motion.div>

            {isMobile ? (
              <motion.div
                whileHover={{ rotate: 90 }}
                whileTap={{ scale: 0.9 }}
              >
                <IconButton
                  color="inherit"
                  aria-label="open drawer"
                  edge="end"
                  onClick={handleDrawerToggle}
                  sx={{
                    color: '#ffffff',
                    transition: 'all 0.3s ease-in-out',
                    background: 'rgba(139, 92, 246, 0.1)',
                    border: '1px solid rgba(139, 92, 246, 0.3)',
                    '&:hover': {
                      background: 'rgba(139, 92, 246, 0.2)',
                      boxShadow: '0 0 20px rgba(139, 92, 246, 0.4)'
                    }
                  }}
                >
                  <MenuIcon />
                </IconButton>
              </motion.div>
            ) : (
              <Box sx={{ display: 'flex', gap: 1.5 }}>
                  {menuItems.map((item, index) => (
                    <motion.div
                      key={item.text}
                      initial={{ opacity: 0, y: -20 }}
                      animate={{ opacity: 1, y: 0 }}
                      transition={{ delay: index * 0.1, duration: 0.5 }}
                      whileHover={{ 
                        scale: 1.05,
                        transition: { duration: 0.3 }
                      }}
                      whileTap={{ scale: 0.95 }}
                    >
                      <Button
                        component={Link}
                        to={item.path}
                        sx={{
                          color: location.pathname === item.path ? '#ffffff' : '#cbd5e1',
                          fontWeight: location.pathname === item.path ? '700' : '500',
                          position: 'relative',
                          px: 3,
                          py: 1.5,
                          fontSize: '1rem',
                          textTransform: 'none',
                          minWidth: 'auto',
                          borderRadius: '12px',
                          transition: 'all 0.4s cubic-bezier(0.4, 0, 0.2, 1)',
                          letterSpacing: '0.01em',
                          fontFamily: '"Inter", system-ui, sans-serif',
                          overflow: 'hidden',
                          '&::before': {
                            content: '""',
                            position: 'absolute',
                            inset: 0,
                            borderRadius: '12px',
                            padding: '1px',
                            background: location.pathname === item.path 
                              ? 'linear-gradient(135deg, rgba(168, 85, 247, 0.5), rgba(236, 72, 153, 0.5))'
                              : 'linear-gradient(135deg, rgba(148, 163, 184, 0), rgba(148, 163, 184, 0))',
                            WebkitMask: 'linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0)',
                            WebkitMaskComposite: 'xor',
                            maskComposite: 'exclude',
                            transition: 'all 0.4s ease',
                            opacity: location.pathname === item.path ? 1 : 0
                          },
                          '&::after': {
                            content: '""',
                            position: 'absolute',
                            inset: 0,
                            borderRadius: '12px',
                            background: location.pathname === item.path 
                              ? 'linear-gradient(135deg, rgba(139, 92, 246, 0.15), rgba(236, 72, 153, 0.1))'
                              : 'transparent',
                            transition: 'all 0.4s ease',
                            zIndex: -1
                          },
                          '&:hover': {
                            color: '#ffffff',
                            transform: 'translateY(-2px)',
                            backgroundColor: 'rgba(139, 92, 246, 0.15)',
                            boxShadow: '0 8px 24px rgba(139, 92, 246, 0.25), 0 0 0 1px rgba(168, 85, 247, 0.3)',
                            '&::before': {
                              background: 'linear-gradient(135deg, rgba(168, 85, 247, 0.6), rgba(236, 72, 153, 0.6))',
                              opacity: 1
                            },
                            '&::after': {
                              background: 'linear-gradient(135deg, rgba(139, 92, 246, 0.2), rgba(236, 72, 153, 0.15))'
                            },
                            '& .nav-dot': {
                              opacity: 1,
                              transform: 'translateX(-50%) scale(1)'
                            }
                          },
                          '& .nav-dot': {
                            position: 'absolute',
                            bottom: -12,
                            left: '50%',
                            width: '6px',
                            height: '6px',
                            borderRadius: '50%',
                            background: 'linear-gradient(135deg, #a855f7, #ec4899)',
                            transform: location.pathname === item.path ? 'translateX(-50%) scale(1)' : 'translateX(-50%) scale(0)',
                            opacity: location.pathname === item.path ? 1 : 0,
                            transition: 'all 0.4s cubic-bezier(0.4, 0, 0.2, 1)',
                            boxShadow: location.pathname === item.path 
                              ? '0 0 20px rgba(168, 85, 247, 0.8), 0 0 40px rgba(168, 85, 247, 0.4)'
                              : 'none'
                          }
                        }}
                      >
                        {item.text}
                        <Box className="nav-dot" />
                      </Button>
                    </motion.div>
                  ))}
              </Box>
            )}
          </Toolbar>
        </AppBar>
      </motion.div>

      <AnimatePresence>
        {mobileOpen && (
          <Drawer
            variant="temporary"
            anchor="right"
            open={mobileOpen}
            onClose={handleDrawerToggle}
            ModalProps={{
              keepMounted: true,
            }}
            sx={{
              display: { xs: 'block', md: 'none' },
              '& .MuiDrawer-paper': { 
                boxSizing: 'border-box', 
                width: 280,
                background: 'linear-gradient(135deg, #4f46e5 0%, #7c3aed 50%, #ec4899 100%)',
                backdropFilter: 'none'
              },
            }}
          >
            <motion.div
              initial={{ x: 280 }}
              animate={{ x: 0 }}
              exit={{ x: 280 }}
              transition={{ duration: 0.3, ease: "easeInOut" }}
            >
              {drawer}
            </motion.div>
          </Drawer>
        )}
      </AnimatePresence>
    </>
  );
};

export default Navbar;