import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Typography,
  Button,
  Grid,
  Chip,
  Rating
} from '@mui/material';
import {
  CheckCircle,
  TrendingUp,
  Code,
  SmartToy,
  Phone
} from '@mui/icons-material';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import axios from 'axios';
import AnimatedHero from '../components/AnimatedHero';
import AnimatedCard from '../components/AnimatedCard';
import DarkExpertise from '../components/DarkExpertise';
import DarkCaseStudies from '../components/DarkCaseStudies';

const Home = () => {
  const [testimonials, setTestimonials] = useState([]);
  const [projects, setProjects] = useState([]);

  useEffect(() => {
    // Fetch testimonials
    axios.get('/api/testimonials')
      .then(response => setTestimonials(response.data.slice(0, 3)))
      .catch(error => console.error('Error fetching testimonials:', error));

    // Fetch featured projects
    axios.get('/api/projects/featured')
      .then(response => setProjects(response.data))
      .catch(error => console.error('Error fetching projects:', error));
  }, []);

  const services = [
    {
      icon: <TrendingUp sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: 'SEO, AEO & GEO Optimization',
      description: 'We optimize your digital presence for traditional search engines and modern AI-powered answer engines to ensure consistent visibility and long-term growth.'
    },
    {
      icon: <Code sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: 'Web Development',
      description: 'We design and develop fast, secure, and SEO-optimized websites and platforms that convert visitors into customers.'
    },
    {
      icon: <Phone sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: 'App Development',
      description: 'We build scalable and high-performance mobile applications tailored to business requirements.'
    },
    {
      icon: <SmartToy sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: 'AI & Intelligent Solutions',
      description: 'We develop custom AI solutions including chatbots, automation systems, computer vision, and generative AI applications.'
    }
  ];

  const trustPoints = [
    'AI-First Engineering',
    'SEO & Growth Expertise',
    'Transparent Delivery'
  ];

  return (
    <Box sx={{ backgroundColor: '#030303' }}>
      {/* Animated Hero Section */}
      <AnimatedHero />

      {/* Trust Statement */}
      <Box sx={{ py: 6, backgroundColor: '#050505' }}>
        <Container maxWidth="lg">
          <motion.div
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
            viewport={{ once: true }}
          >
            <Typography 
              variant="h4" 
              textAlign="center" 
              gutterBottom 
              sx={{ 
                color: 'white',
                fontSize: { xs: '1.5rem', md: '2rem' },
                fontWeight: 'bold'
              }}
            >
              Trusted by startups, growing businesses, and technology-led organizations
            </Typography>
          </motion.div>
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.2 }}
            viewport={{ once: true }}
          >
            <Typography 
              variant="h6" 
              textAlign="center" 
              sx={{ 
                mb: 4, 
                color: '#9ca3af',
                fontSize: { xs: '1rem', md: '1.25rem' }
              }}
            >
              for delivering reliable, scalable, and future-ready digital solutions.
            </Typography>
          </motion.div>
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.4 }}
            viewport={{ once: true }}
          >
            <Box sx={{ display: 'flex', justifyContent: 'center', gap: 4, flexWrap: 'wrap' }}>
              {trustPoints.map((point, index) => (
                <motion.div
                  key={index}
                  initial={{ opacity: 0, scale: 0.8 }}
                  whileInView={{ opacity: 1, scale: 1 }}
                  transition={{ duration: 0.4, delay: index * 0.1 }}
                  viewport={{ once: true }}
                  whileHover={{ scale: 1.05 }}
                  style={{ display: 'flex', alignItems: 'center', gap: 8 }}
                >
                  <CheckCircle sx={{ color: '#10b981' }} />
                  <Typography variant="body1" fontWeight="medium" sx={{ color: 'white' }}>
                    {point}
                  </Typography>
                </motion.div>
              ))}
            </Box>
          </motion.div>
        </Container>
      </Box>

      {/* Dark Expertise Section */}
      <DarkExpertise />

      {/* Why Cognix AI */}
      <Box sx={{ py: 8, backgroundColor: '#050505' }}>
        <Container maxWidth="lg">
          <motion.div
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
            viewport={{ once: true }}
          >
            <Typography 
              variant="h3" 
              textAlign="center" 
              gutterBottom 
              sx={{ 
                color: 'white',
                fontSize: { xs: '2rem', md: '3rem' },
                fontWeight: 'bold'
              }}
            >
              Why Businesses Choose{' '}
              <Box component="span" sx={{ color: '#8b5cf6' }}>
                Cognix AI
              </Box>
            </Typography>
          </motion.div>
          <Grid container spacing={3} sx={{ mt: 2 }}>
            {[
              'AI-first and engineering-driven approach',
              'Data-backed SEO strategies',
              'Custom-built, scalable solutions',
              'Clear communication and accountability',
              'Focus on long-term business value'
            ].map((reason, index) => (
              <Grid item xs={12} md={6} lg={4} key={index}>
                <motion.div
                  initial={{ opacity: 0, y: 30 }}
                  whileInView={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.6, delay: index * 0.1 }}
                  viewport={{ once: true }}
                  whileHover={{ 
                    scale: 1.02,
                    transition: { duration: 0.2 }
                  }}
                >
                  <Box
                    sx={{
                      p: 4,
                      borderRadius: 4,
                      background: 'rgba(255, 255, 255, 0.05)',
                      border: '1px solid rgba(255, 255, 255, 0.1)',
                      transition: 'all 0.3s ease-in-out',
                      height: '100%',
                      textAlign: 'center',
                      display: 'flex',
                      flexDirection: 'column',
                      justifyContent: 'center',
                      '&:hover': {
                        borderColor: 'rgba(139, 92, 246, 0.5)',
                        '& .reason-icon': {
                          transform: 'scale(1.1)',
                        }
                      }
                    }}
                  >
                    <CheckCircle 
                      className="reason-icon"
                      sx={{ 
                        fontSize: 40, 
                        mb: 2, 
                        mx: 'auto',
                        color: '#8b5cf6',
                        transition: 'transform 0.3s ease-in-out'
                      }} 
                    />
                    <Typography 
                      variant="h6" 
                      sx={{ 
                        color: 'white',
                        fontWeight: 'bold'
                      }}
                    >
                      {reason}
                    </Typography>
                  </Box>
                </motion.div>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>

      {/* Dark Case Studies */}
      <DarkCaseStudies />

      {/* Client Testimonials */}
      {testimonials.length > 0 && (
        <Box sx={{ py: 8, backgroundColor: '#030303' }}>
          <Container maxWidth="lg">
            <motion.div
              initial={{ opacity: 0, y: 30 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6 }}
              viewport={{ once: true }}
            >
              <Typography 
                variant="h3" 
                textAlign="center" 
                gutterBottom 
                sx={{ 
                  color: 'white',
                  fontSize: { xs: '2rem', md: '3rem' },
                  fontWeight: 'bold'
                }}
              >
                What Our Clients Say
              </Typography>
            </motion.div>
            <Grid container spacing={4} sx={{ mt: 2 }}>
              {testimonials.map((testimonial, index) => (
                <Grid item xs={12} md={4} key={testimonial._id}>
                  <motion.div
                    initial={{ opacity: 0, y: 30 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.6, delay: index * 0.1 }}
                    viewport={{ once: true }}
                    whileHover={{ 
                      scale: 1.02,
                      transition: { duration: 0.2 }
                    }}
                  >
                    <Box
                      sx={{
                        p: 4,
                        borderRadius: 4,
                        background: 'rgba(255, 255, 255, 0.05)',
                        border: '1px solid rgba(255, 255, 255, 0.1)',
                        transition: 'all 0.3s ease-in-out',
                        height: '100%',
                        display: 'flex',
                        flexDirection: 'column',
                        '&:hover': {
                          borderColor: 'rgba(139, 92, 246, 0.5)'
                        }
                      }}
                    >
                      <Rating value={testimonial.rating} readOnly sx={{ mb: 2 }} />
                      <Typography 
                        variant="body1" 
                        sx={{ 
                          mb: 2, 
                          fontStyle: 'italic', 
                          flexGrow: 1,
                          color: '#d1d5db'
                        }}
                      >
                        "{testimonial.message}"
                      </Typography>
                      <Box sx={{ mt: 'auto' }}>
                        <Typography 
                          variant="subtitle2" 
                          fontWeight="bold" 
                          sx={{ color: 'white' }}
                        >
                           {testimonial.name}, {testimonial.position}
                        </Typography>
                        <Typography 
                          variant="body2" 
                          sx={{ color: '#9ca3af' }}
                        >
                          {testimonial.company}
                        </Typography>
                      </Box>
                    </Box>
                  </motion.div>
                </Grid>
              ))}
            </Grid>
          </Container>
        </Box>
      )}

      {/* CTA Section */}
      <Box
        sx={{
          background: 'linear-gradient(135deg, rgba(88, 28, 135, 0.8) 0%, rgba(30, 58, 138, 0.8) 100%)',
          color: 'white',
          py: 8,
          textAlign: 'center',
          position: 'relative',
          overflow: 'hidden'
        }}
      >
        <Container maxWidth="lg" sx={{ position: 'relative', zIndex: 1 }}>
          <motion.div
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
            viewport={{ once: true }}
          >
            <Typography 
              variant="h3" 
              gutterBottom
              sx={{ 
                fontSize: { xs: '2rem', md: '3rem' },
                fontWeight: 'bold',
                color: 'white'
              }}
            >
              Let's Build Intelligent Solutions Together
            </Typography>
          </motion.div>
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.2 }}
            viewport={{ once: true }}
          >
            <Typography 
              variant="h6" 
              sx={{ 
                mb: 4,
                opacity: 0.95,
                color: '#e5e7eb',
                fontSize: { xs: '1rem', md: '1.25rem' }
              }}
            >
              Partner with Cognix AI to unlock growth through technology and artificial intelligence.
            </Typography>
          </motion.div>
          <motion.div
            initial={{ opacity: 0, scale: 0.8 }}
            whileInView={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.6, delay: 0.4 }}
            viewport={{ once: true }}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
          >
            <Button
              variant="contained"
              size="large"
              component={Link}
              to="/contact"
              sx={{ 
                backgroundColor: 'white', 
                color: '#8b5cf6',
                px: 4,
                py: 1.5,
                fontSize: '1.1rem',
                fontWeight: 'bold',
                borderRadius: 3,
                boxShadow: '0 8px 32px rgba(0,0,0,0.1)',
                '&:hover': { 
                  backgroundColor: '#faf5ff',
                  boxShadow: '0 12px 40px rgba(0,0,0,0.2)'
                }
              }}
            >
              Contact Us
            </Button>
          </motion.div>
        </Container>
        
        {/* Animated background elements */}
        <motion.div
          animate={{
            y: [0, -20, 0],
            rotate: [0, 180, 360]
          }}
          transition={{
            repeat: Infinity,
            duration: 8,
            ease: "easeInOut"
          }}
          style={{
            position: 'absolute',
            top: '10%',
            left: '5%',
            width: '100px',
            height: '100px',
            borderRadius: '50%',
            background: 'rgba(255,255,255,0.1)',
            filter: 'blur(40px)'
          }}
        />
        <motion.div
          animate={{
            y: [0, 20, 0],
            x: [0, -10, 0],
            rotate: [360, 180, 0]
          }}
          transition={{
            repeat: Infinity,
            duration: 6,
            ease: "easeInOut"
          }}
          style={{
            position: 'absolute',
            bottom: '15%',
            right: '10%',
            width: '80px',
            height: '80px',
            borderRadius: '50%',
            background: 'rgba(255,255,255,0.1)',
            filter: 'blur(30px)'
          }}
        />
      </Box>

    </Box>
  );
};

export default Home;