import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Typography,
  Button
} from '@mui/material';
import { motion, AnimatePresence } from 'framer-motion';
import { Link } from 'react-router-dom';

const AnimatedHero = () => {
  const [textIndex, setTextIndex] = useState(0);

  const animatedTexts = [
    'Smart Automation Systems',
    'Intelligent SEO Solutions', 
    'AI-Powered Development',
    'Digital Transformation'
  ];

  useEffect(() => {
    // Animate text rotation
    const interval = setInterval(() => {
      setTextIndex((prev) => (prev + 1) % animatedTexts.length);
    }, 3000);

    return () => clearInterval(interval);
  }, [animatedTexts.length]);

  return (
    <Box
      sx={{
        position: 'relative',
        minHeight: '100vh',
        width: '100%',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: '#030303',
        overflow: 'hidden'
      }}
    >
      {/* Background Ambient Glows */}
      <Box
        sx={{
          position: 'absolute',
          top: '-10%',
          left: '-10%',
          width: '600px',
          height: '600px',
          background: 'rgba(88, 28, 135, 0.2)',
          filter: 'blur(120px)',
          borderRadius: '50%'
        }}
      />
      <Box
        sx={{
          position: 'absolute',
          bottom: '-10%',
          right: '-10%',
          width: '500px',
          height: '500px',
          background: 'rgba(30, 58, 138, 0.2)',
          filter: 'blur(120px)',
          borderRadius: '50%'
        }}
      />

      {/* Main Content */}
      <Container maxWidth="lg" sx={{ position: 'relative', zIndex: 10, textAlign: 'center', px: 3 }}>
        {/* Top Badge */}
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          style={{ marginBottom: '32px' }}
        >
          <Box
            sx={{
              display: 'inline-flex',
              alignItems: 'center',
              gap: 1,
              px: 1.5,
              py: 0.5,
              borderRadius: '50px',
              border: '1px solid rgba(255, 255, 255, 0.1)',
              background: 'rgba(255, 255, 255, 0.05)',
              backdropFilter: 'blur(10px)'
            }}
          >
            <Box sx={{ position: 'relative', display: 'flex', width: 8, height: 8 }}>
              <Box
                sx={{
                  position: 'absolute',
                  width: '100%',
                  height: '100%',
                  borderRadius: '50%',
                  background: '#a855f7',
                  opacity: 0.75,
                  animation: 'ping 1s cubic-bezier(0, 0, 0.2, 1) infinite',
                  '@keyframes ping': {
                    '75%, 100%': {
                      transform: 'scale(2)',
                      opacity: 0
                    }
                  }
                }}
              />
              <Box
                sx={{
                  position: 'relative',
                  width: 8,
                  height: 8,
                  borderRadius: '50%',
                  background: '#8b5cf6'
                }}
              />
            </Box>
            <Typography
              sx={{
                fontSize: '0.75rem',
                fontWeight: 500,
                color: '#d1d5db',
                letterSpacing: '0.05em',
                textTransform: 'uppercase'
              }}
            >
              Next-Gen Automation
            </Typography>
          </Box>
        </motion.div>

        {/* Headline */}
        <motion.div
          initial={{ opacity: 0, scale: 0.95 }}
          animate={{ opacity: 1, scale: 1 }}
          transition={{ duration: 0.5 }}
        >
          <Typography
            variant="h1"
            sx={{
              fontSize: { xs: '3rem', md: '6rem', lg: '8rem' },
              fontWeight: 900,
              color: 'white',
              textShadow: '0 6px 28px rgba(0, 0, 0, 0.45)',
              mb: 3,
              letterSpacing: '-0.025em',
              lineHeight: 0.9
            }}
          >
            Cognix{' '}
            <Box
              component="span"
              sx={{
                background: 'linear-gradient(to right, #a855f7, #60a5fa, #6366f1)',
                backgroundClip: 'text',
                WebkitBackgroundClip: 'text',
                WebkitTextFillColor: 'transparent'
              }}
            >
              AI
            </Box>
          </Typography>
        </motion.div>

        {/* Sub-Headline with rotation */}
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.2 }}
          style={{ height: '60px', display: 'flex', alignItems: 'center', justifyContent: 'center', marginBottom: '16px' }}
        >
          <AnimatePresence mode="wait">
            <motion.div
              key={textIndex}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.5 }}
            >
              <Typography
                sx={{
                  fontSize: { xs: '1.25rem', md: '2rem' },
                  fontWeight: 500,
                  color: '#e5e7eb'
                }}
              >
                {animatedTexts[textIndex]}
              </Typography>
            </motion.div>
          </AnimatePresence>
        </motion.div>

        {/* Description */}
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.3 }}
        >
          <Typography
            sx={{
              color: '#cbd5e1',
              fontSize: '1.125rem',
              maxWidth: '32rem',
              mx: 'auto',
              lineHeight: 1.6,
              mb: 5
            }}
          >
            We are an AI-driven technology agency helping businesses increase visibility, 
            build scalable digital products, and automate operations using cutting-edge SEO strategies 
            and intelligent AI systems.
          </Typography>
        </motion.div>

        {/* CTA Section */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.4 }}
          style={{
            display: 'flex',
            flexDirection: 'row',
            gap: '16px',
            justifyContent: 'center',
            flexWrap: 'wrap'
          }}
        >
          <motion.div
            whileHover={{ 
              scale: 1.05,
              boxShadow: '0 0 30px rgba(255, 255, 255, 0.2)'
            }}
            whileTap={{ scale: 0.95 }}
          >
            <Button
              component={Link}
              to="/contact"
              sx={{
                px: 4,
                py: 2,
                backgroundColor: 'white',
                color: 'black',
                fontWeight: 'bold',
                borderRadius: '50px',
                fontSize: '1rem',
                textTransform: 'none',
                '&:hover': {
                  backgroundColor: '#faf5ff',
                  boxShadow: '0 0 30px rgba(255, 255, 255, 0.2)'
                },
                transition: 'all 0.3s ease-in-out'
              }}
            >
              Get Free Consultation
            </Button>
          </motion.div>

          <motion.div
            whileHover={{ 
              scale: 1.05,
              backgroundColor: 'rgba(255, 255, 255, 0.1)'
            }}
            whileTap={{ scale: 0.95 }}
          >
            <Button
              component={Link}
              to="/contact"
              sx={{
                px: 4,
                py: 2,
                backgroundColor: 'rgba(255, 255, 255, 0.05)',
                color: 'white',
                fontWeight: 'bold',
                borderRadius: '50px',
                fontSize: '1rem',
                textTransform: 'none',
                border: '1px solid rgba(255, 255, 255, 0.1)',
                backdropFilter: 'blur(10px)',
                '&:hover': {
                  backgroundColor: 'rgba(255, 255, 255, 0.1)'
                },
                transition: 'all 0.3s ease-in-out'
              }}
            >
              Talk to Our AI Expert
            </Button>
          </motion.div>
        </motion.div>

        {/* Trust Indicator */}
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ delay: 0.6 }}
        >
          <Typography
            sx={{
              mt: 8,
              fontSize: '0.875rem',
              color: '#94a3b8',
              fontWeight: 500,
              letterSpacing: '0.1em',
              textTransform: 'uppercase'
            }}
          >
            Powering the next generation of digital scale
          </Typography>
        </motion.div>
      </Container>

      {/* Grid Overlay for Texture */}
      <Box
        sx={{
          position: 'absolute',
          inset: 0,
          backgroundImage: 'url("data:image/svg+xml,%3Csvg width="60" height="60" viewBox="0 0 60 60" xmlns="http://www.w3.org/2000/svg"%3E%3Cg fill="none" fill-rule="evenodd"%3E%3Cg fill="%23ffffff" fill-opacity="0.03"%3E%3Ccircle cx="30" cy="30" r="1.5"/%3E%3C/g%3E%3C/g%3E%3C/svg%3E")',
          opacity: 0.2,
          pointerEvents: 'none'
        }}
      />
    </Box>
  );
};

export default AnimatedHero;