import React from 'react';
import { Box, Container, Typography, Paper, Divider } from '@mui/material';

const PrivacyPolicy = () => {
  return (
    <Box sx={{ py: 8, backgroundColor: '#030303', minHeight: '100vh' }}>
      <Container maxWidth="lg">
        <Paper sx={{ p: { xs: 3, md: 6 }, backgroundColor: '#0b0b10', color: '#e5e7eb' }}>
          <Typography variant="h3" gutterBottom sx={{ color: '#ffffff', fontWeight: 'bold', mb: 3 }}>
            Privacy Policy
          </Typography>
          <Typography variant="body2" sx={{ color: '#9ca3af', mb: 4 }}>
            Last Updated: February 4, 2026
          </Typography>

          <Divider sx={{ my: 4, backgroundColor: 'rgba(255,255,255,0.1)' }} />

          <Typography variant="h5" gutterBottom sx={{ color: '#ffffff', mt: 4, mb: 2 }}>
            1. Information We Collect
          </Typography>
          <Typography variant="body1" paragraph sx={{ color: '#d1d5db', lineHeight: 1.8 }}>
            At Cognix AI, we collect information that you provide directly to us, including:
          </Typography>
          <Box component="ul" sx={{ pl: 4, color: '#d1d5db', lineHeight: 1.8 }}>
            <li>Name, email address, and phone number when you contact us</li>
            <li>Business information when you request services</li>
            <li>Communication preferences and feedback</li>
            <li>Technical information about your device and browsing activity</li>
          </Box>

          <Typography variant="h5" gutterBottom sx={{ color: '#ffffff', mt: 4, mb: 2 }}>
            2. How We Use Your Information
          </Typography>
          <Typography variant="body1" paragraph sx={{ color: '#d1d5db', lineHeight: 1.8 }}>
            We use the information we collect to:
          </Typography>
          <Box component="ul" sx={{ pl: 4, color: '#d1d5db', lineHeight: 1.8 }}>
            <li>Provide, maintain, and improve our services</li>
            <li>Respond to your inquiries and fulfill your requests</li>
            <li>Send you technical notices and support messages</li>
            <li>Communicate with you about products, services, and events</li>
            <li>Monitor and analyze trends, usage, and activities</li>
          </Box>

          <Typography variant="h5" gutterBottom sx={{ color: '#ffffff', mt: 4, mb: 2 }}>
            3. Information Sharing
          </Typography>
          <Typography variant="body1" paragraph sx={{ color: '#d1d5db', lineHeight: 1.8 }}>
            We do not sell, trade, or rent your personal information to third parties. We may share your information only in the following circumstances:
          </Typography>
          <Box component="ul" sx={{ pl: 4, color: '#d1d5db', lineHeight: 1.8 }}>
            <li>With your consent</li>
            <li>To comply with legal obligations</li>
            <li>To protect our rights and safety</li>
            <li>With service providers who assist in our operations</li>
          </Box>

          <Typography variant="h5" gutterBottom sx={{ color: '#ffffff', mt: 4, mb: 2 }}>
            4. Data Security
          </Typography>
          <Typography variant="body1" paragraph sx={{ color: '#d1d5db', lineHeight: 1.8 }}>
            We implement appropriate technical and organizational measures to protect your personal information against unauthorized access, alteration, disclosure, or destruction. However, no method of transmission over the Internet is 100% secure.
          </Typography>

          <Typography variant="h5" gutterBottom sx={{ color: '#ffffff', mt: 4, mb: 2 }}>
            5. Your Rights
          </Typography>
          <Typography variant="body1" paragraph sx={{ color: '#d1d5db', lineHeight: 1.8 }}>
            You have the right to:
          </Typography>
          <Box component="ul" sx={{ pl: 4, color: '#d1d5db', lineHeight: 1.8 }}>
            <li>Access your personal information</li>
            <li>Correct inaccurate information</li>
            <li>Request deletion of your information</li>
            <li>Opt-out of marketing communications</li>
            <li>Lodge a complaint with supervisory authorities</li>
          </Box>

          <Typography variant="h5" gutterBottom sx={{ color: '#ffffff', mt: 4, mb: 2 }}>
            6. Cookies and Tracking
          </Typography>
          <Typography variant="body1" paragraph sx={{ color: '#d1d5db', lineHeight: 1.8 }}>
            We use cookies and similar tracking technologies to track activity on our website and hold certain information. You can instruct your browser to refuse all cookies or indicate when a cookie is being sent.
          </Typography>

          <Typography variant="h5" gutterBottom sx={{ color: '#ffffff', mt: 4, mb: 2 }}>
            7. Changes to This Policy
          </Typography>
          <Typography variant="body1" paragraph sx={{ color: '#d1d5db', lineHeight: 1.8 }}>
            We may update this Privacy Policy from time to time. We will notify you of any changes by posting the new Privacy Policy on this page and updating the "Last Updated" date.
          </Typography>

          <Typography variant="h5" gutterBottom sx={{ color: '#ffffff', mt: 4, mb: 2 }}>
            8. Contact Us
          </Typography>
          <Typography variant="body1" paragraph sx={{ color: '#d1d5db', lineHeight: 1.8 }}>
            If you have any questions about this Privacy Policy, please contact us:
          </Typography>
          <Box sx={{ pl: 4, color: '#d1d5db', lineHeight: 1.8 }}>
            <Typography variant="body1">Email: cognixpvtltb@gmail.com</Typography>
            <Typography variant="body1">Phone: +91 7991955775</Typography>
          </Box>
        </Paper>
      </Container>
    </Box>
  );
};

export default PrivacyPolicy;
