import React, { useState } from 'react';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  TextField,
  Button,
  MenuItem,
  Alert,
  CircularProgress,
  IconButton
} from '@mui/material';
import {
  LocationOn,
  Email,
  Phone,
  WhatsApp,
  LinkedIn,
  Instagram,
  GitHub,
  Twitter
} from '@mui/icons-material';
import axios from 'axios';

const Contact = () => {
  const [formData, setFormData] = useState({
    fullName: '',
    email: '',
    phone: '',
    companyName: '',
    serviceInterested: '',
    message: ''
  });
  const [loading, setLoading] = useState(false);
  const [alert, setAlert] = useState({ show: false, type: '', message: '' });

  const services = [
    'SEO/AEO/GEO',
    'Web Development',
    'App Development',
    'AI Solutions',
    'Other'
  ];

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setAlert({ show: false, type: '', message: '' });

    try {
      const response = await axios.post('/api/contact', formData);
      setAlert({
        show: true,
        type: 'success',
        message: response.data.message
      });
      setFormData({
        fullName: '',
        email: '',
        phone: '',
        companyName: '',
        serviceInterested: '',
        message: ''
      });
    } catch (error) {
      setAlert({
        show: true,
        type: 'error',
        message: error.response?.data?.message || 'Something went wrong. Please try again.'
      });
    } finally {
      setLoading(false);
    }
  };

  const contactInfo = [
    {
      icon: <LocationOn sx={{ fontSize: 30, color: 'primary.main' }} />,
      title: 'Address',
      details: ['Cognix AI', '3rd Floor, Tech Park', 'Noida, Uttar Pradesh  201301']
    },
    {
      icon: <Email sx={{ fontSize: 30, color: 'primary.main' }} />,
      title: 'Email',
      details: ['cognixpvtltb@gmail.com']
    },
    {
      icon: <Phone sx={{ fontSize: 30, color: 'primary.main' }} />,
      title: 'Phone',
      details: ['+91 7991955775']
    }
  ];

  const socialLinks = [
    { icon: <WhatsApp />, label: 'WhatsApp', href: '#' },
    { icon: <LinkedIn />, label: 'LinkedIn', href: '#' },
    { icon: <Instagram />, label: 'Instagram', href: '#' },
    { icon: <GitHub />, label: 'GitHub', href: '#' },
    { icon: <Twitter />, label: 'Twitter', href: '#' }
  ];

  const inputFieldStyles = {
    '& .MuiInputBase-root': { 
      backgroundColor: 'rgba(240,240,255,0.98)', 
      color: '#1a1a2e',
      border: '2px solid rgba(139, 92, 246, 0.3)',
      borderRadius: 2,
      transition: 'all 0.3s ease',
      '&:hover': {
        borderColor: 'rgba(139, 92, 246, 0.6)',
        backgroundColor: '#ffffff'
      },
      '&.Mui-focused': {
        borderColor: 'rgba(139, 92, 246, 0.9)',
        backgroundColor: '#ffffff',
        boxShadow: '0 0 0 4px rgba(139, 92, 246, 0.15)'
      }
    },
    '& .MuiInputBase-input': {
      padding: '16px 14px'
    },
    '& .MuiInputBase-inputMultiline': {
      padding: '0'
    },
    '& .MuiInputLabel-root': { 
      color: '#475569',
      fontWeight: 500,
      '&.Mui-focused': {
        color: '#7c3aed',
        fontWeight: 600
      }
    },
    '& .MuiOutlinedInput-notchedOutline': { 
      borderColor: 'transparent'
    },
    '& .MuiSelect-icon': {
      color: '#7c3aed'
    }
  };

  return (
    <Box sx={{ backgroundColor: '#030303' }}>
      {/* Hero Section */}
      <Box
        sx={{
          background: 'linear-gradient(135deg, rgba(88, 28, 135, 0.9) 0%, rgba(30, 58, 138, 0.9) 100%)',
          color: 'white',
          py: { xs: 7, md: 9 },
          textAlign: 'center',
          borderBottom: '1px solid rgba(255,255,255,0.06)'
        }}
      >
        <Container maxWidth="lg">
          <Typography variant="h2" component="h1" gutterBottom sx={{ fontWeight: 'bold', color: '#ffffff' }}>
            Get in Touch
          </Typography>
          <Typography variant="h5" sx={{ opacity: 0.95, color: '#e5e7eb' }}>
            Have a project or idea in mind? We'd love to hear from you.
          </Typography>
        </Container>
      </Box>

      {/* Contact Form and Info */}
      <Box sx={{ py: 8, backgroundColor: '#050505' }}>
        <Container maxWidth="lg">
          <Grid container spacing={6}>
            {/* Contact Form */}
            <Grid item xs={12} md={8}>
              <Card sx={{ backgroundColor: 'rgba(255,255,255,0.04)', border: '1px solid rgba(255,255,255,0.08)' }}>
                <CardContent sx={{ p: 4 }}>
                  <Typography variant="h4" gutterBottom sx={{ color: 'white', fontWeight: 'bold' }}>
                    Send us a Message
                  </Typography>
                  <Typography variant="body1" sx={{ mb: 4, color: '#cbd5e1' }}>
                    Fill out the form below and our team will get back to you shortly.
                  </Typography>

                  {alert.show && (
                    <Alert severity={alert.type} sx={{ mb: 3 }}>
                      {alert.message}
                    </Alert>
                  )}

                  <form onSubmit={handleSubmit}>
                    <Grid container spacing={3}>
                      <Grid item xs={12} md={6}>
                        <TextField
                          fullWidth
                          label="Full Name"
                          name="fullName"
                          value={formData.fullName}
                          onChange={handleChange}
                          required
                          sx={inputFieldStyles}
                        />
                      </Grid>
                      <Grid item xs={12} md={6}>
                        <TextField
                          fullWidth
                          label="Email Address"
                          name="email"
                          type="email"
                          value={formData.email}
                          onChange={handleChange}
                          required
                          sx={inputFieldStyles}
                        />
                      </Grid>
                      <Grid item xs={12} md={6}>
                        <TextField
                          fullWidth
                          label="Phone Number"
                          name="phone"
                          value={formData.phone}
                          onChange={handleChange}
                          required
                          sx={inputFieldStyles}
                        />
                      </Grid>
                      <Grid item xs={12} md={6}>
                        <TextField
                          fullWidth
                          label="Company Name"
                          name="companyName"
                          value={formData.companyName}
                          onChange={handleChange}
                          sx={inputFieldStyles}
                        />
                      </Grid>
                      <Grid item xs={12}>
                        <TextField
                          fullWidth
                          select
                          label="Service Interested In"
                          name="serviceInterested"
                          value={formData.serviceInterested}
                          onChange={handleChange}
                          required
                          sx={inputFieldStyles}
                          SelectProps={{
                            MenuProps: {
                              PaperProps: {
                                sx: {
                                  backgroundColor: '#ffffff',
                                  boxShadow: '0 8px 32px rgba(139, 92, 246, 0.2)',
                                  border: '2px solid rgba(139, 92, 246, 0.2)',
                                  borderRadius: 2,
                                  mt: 1,
                                  '& .MuiMenuItem-root': {
                                    color: '#1a1a2e',
                                    fontWeight: 500,
                                    '&:hover': {
                                      backgroundColor: 'rgba(139, 92, 246, 0.1)'
                                    },
                                    '&.Mui-selected': {
                                      backgroundColor: 'rgba(139, 92, 246, 0.2)',
                                      '&:hover': {
                                        backgroundColor: 'rgba(139, 92, 246, 0.3)'
                                      }
                                    }
                                  }
                                }
                              }
                            }
                          }}
                        >
                          {services.map((service) => (
                            <MenuItem key={service} value={service}>
                              {service}
                            </MenuItem>
                          ))}
                        </TextField>
                      </Grid>
                      <Grid item xs={12}>
                        <TextField
                          fullWidth
                          multiline
                          rows={4}
                          label="Message"
                          name="message"
                          value={formData.message}
                          onChange={handleChange}
                          required
                          sx={inputFieldStyles}
                        />
                      </Grid>
                      <Grid item xs={12}>
                        <Button
                          type="submit"
                          variant="contained"
                          size="large"
                          disabled={loading}
                          sx={{ 
                            minWidth: 150,
                            background: 'linear-gradient(135deg, #7c3aed, #4f46e5)',
                            color: '#ffffff',
                            fontWeight: 700,
                            fontSize: '1rem',
                            px: 4,
                            py: 1.5,
                            borderRadius: 2,
                            boxShadow: '0 4px 20px rgba(124, 58, 237, 0.4)',
                            transition: 'all 0.3s ease',
                            '&:hover': {
                              background: 'linear-gradient(135deg, #6d28d9, #4338ca)',
                              boxShadow: '0 6px 30px rgba(124, 58, 237, 0.6)',
                              transform: 'translateY(-2px)'
                            },
                            '&:disabled': {
                              background: 'rgba(139, 92, 246, 0.3)',
                              color: 'rgba(255,255,255,0.5)'
                            }
                          }}
                        >
                          {loading ? <CircularProgress size={24} /> : 'Send Message'}
                        </Button>
                      </Grid>
                    </Grid>
                  </form>
                </CardContent>
              </Card>
            </Grid>

            {/* Contact Information */}
            <Grid item xs={12} md={4}>
              <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
                {contactInfo.map((info, index) => (
                  <Card key={index} sx={{ backgroundColor: 'rgba(255,255,255,0.04)', border: '1px solid rgba(255,255,255,0.08)' }}>
                    <CardContent sx={{ textAlign: 'center', p: 3 }}>
                      {info.icon}
                      <Typography variant="h6" sx={{ mt: 2, mb: 1, fontWeight: 'bold', color: 'white' }}>
                        {info.title}
                      </Typography>
                      {info.details.map((detail, idx) => (
                        <Typography key={idx} variant="body2" sx={{ color: '#cbd5e1' }}>
                          {detail}
                        </Typography>
                      ))}
                    </CardContent>
                  </Card>
                ))}
              </Box>
            </Grid>
          </Grid>
        </Container>
      </Box>

      {/* Map Section (Placeholder) */}
      <Box sx={{ py: 8, backgroundColor: '#030303' }}>
        <Container maxWidth="lg">
          <Typography variant="h4" textAlign="center" gutterBottom sx={{ color: 'white', fontWeight: 'bold' }}>
            Find Us
          </Typography>
          <Box
            sx={{
              height: 450,
              border: '2px solid rgba(139, 92, 246, 0.3)',
              borderRadius: 2,
              overflow: 'hidden',
              mt: 3,
              boxShadow: '0 8px 32px rgba(139, 92, 246, 0.2)'
            }}
          >
            <iframe
              src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d224357.4385393565!2d77.2090212!3d28.5355161!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x390ce5a43173357b%3A0x37ffce30c87cc03f!2sNoida%2C%20Uttar%20Pradesh!5e0!3m2!1sen!2sin!4v1234567890"
              width="100%"
              height="100%"
              style={{ border: 0 }}
              allowFullScreen=""
              loading="lazy"
              referrerPolicy="no-referrer-when-downgrade"
              title="Cognix AI Location"
            />
          </Box>
          <Box sx={{ mt: 3, textAlign: 'center' }}>
            <Typography variant="body1" sx={{ color: '#cbd5e1', mb: 1 }}>
               Noida, Uttar Pradesh, India
            </Typography>
            <Typography variant="body2" sx={{ color: '#9ca3af' }}>
              Visit us or reach out via phone/email for inquiries
            </Typography>
          </Box>
        </Container>
      </Box>
    </Box>
  );
};

export default Contact;