import React from 'react';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  Avatar,
  Paper
} from '@mui/material';
import {
  Engineering,
  Psychology,
  Visibility,
  Public,
  Email,
  WhatsApp
} from '@mui/icons-material';

const About = () => {
  const founders = [
    {
      name: 'Mr. Ravikant Prajapati',
      education: 'B.Tech  Computer Science Engineering',
      position: 'Co-Founder & Technical Lead',
      description: 'Ravikant leads the technical architecture, development, and infrastructure at Cognix AI, ensuring scalable and performance-driven solutions.',
      avatar: '/images/ravikant-avatar.jpg'
    },
    {
      name: 'Mr. Ram Vishwakarma',
      education: 'B.Tech  Artificial Intelligence & Machine Learning',
      position: 'Co-Founder & AI Lead',
      description: 'Ram drives AI strategy and development, specializing in NLP, computer vision, generative AI, and automation systems.',
      avatar: '/images/ram-avatar.jpg'
    }
  ];

  const values = [
    {
      icon: <Engineering sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: 'Technical Excellence',
      description: 'We maintain the highest standards in code quality, architecture, and performance optimization.'
    },
    {
      icon: <Psychology sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: 'AI-First Approach',
      description: 'We leverage artificial intelligence and machine learning to create intelligent, adaptive solutions.'
    },
    {
      icon: <Visibility sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: 'Transparency',
      description: 'We believe in clear communication, honest reporting, and transparent project management.'
    },
    {
      icon: <Public sx={{ fontSize: 40, color: 'primary.main' }} />,
      title: 'Global Impact',
      description: 'We aim to create solutions that make a positive impact on businesses and communities worldwide.'
    }
  ];

  return (
    <Box sx={{ backgroundColor: '#030303' }}>
      {/* Hero Section */}
      <Box
        sx={{
          background: 'linear-gradient(135deg, rgba(88, 28, 135, 0.9) 0%, rgba(30, 58, 138, 0.9) 100%)',
          color: 'white',
          py: { xs: 7, md: 9 },
          textAlign: 'center',
          borderBottom: '1px solid rgba(255,255,255,0.06)'
        }}
      >
        <Container maxWidth="lg">
          <Typography variant="h2" component="h1" gutterBottom sx={{ fontWeight: 'bold', color: 'white' }}>
            About Cognix AI
          </Typography>
          <Typography variant="h5" sx={{ opacity: 0.95, color: '#e5e7eb' }}>
            Technology and AI-focused digital agency specializing in intelligent solutions
          </Typography>
        </Container>
      </Box>

      {/* Company Overview */}
      <Box sx={{ py: 8, backgroundColor: '#050505' }}>
        <Container maxWidth="lg">
          <Grid container spacing={6} alignItems="center">
            <Grid item xs={12} md={6}>
              <Typography variant="h3" gutterBottom sx={{ color: 'white', fontWeight: 'bold' }}>
                Who We Are
              </Typography>
              <Typography variant="body1" paragraph sx={{ color: '#d1d5db' }}>
                Cognix AI is a technology and AI-focused digital agency specializing in SEO, web and app 
                development, and intelligent automation. We work closely with businesses to understand 
                their goals and build solutions that are scalable, secure, and result-driven.
              </Typography>
              <Typography variant="body1" paragraph sx={{ color: '#cbd5e1' }}>
                Our team combines deep technical expertise with strategic thinking to deliver solutions 
                that not only meet current needs but also prepare businesses for future challenges and opportunities.
              </Typography>
            </Grid>
            <Grid item xs={12} md={6}>
              <Box
                sx={{
                  height: 300,
                  background: 'radial-gradient(ellipse at center, rgba(30, 35, 80, 1) 0%, rgba(15, 20, 50, 1) 40%, rgba(5, 10, 25, 1) 100%)',
                  border: '2px solid rgba(139, 92, 246, 0.6)',
                  borderRadius: 2,
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                  justifyContent: 'center',
                  position: 'relative',
                  overflow: 'hidden',
                  boxShadow: '0 0 120px rgba(139, 92, 246, 0.7), inset 0 0 120px rgba(79, 70, 229, 0.4)',
                }}
              >
                {/* Animated particles - falling from top */}
                {[...Array(30)].map((_, i) => (
                  <Box
                    key={i}
                    sx={{
                      position: 'absolute',
                      width: i % 4 === 0 ? '3px' : '2px',
                      height: i % 4 === 0 ? '3px' : '2px',
                      background: i % 3 === 0 ? '#c4b5fd' : i % 3 === 1 ? '#93c5fd' : '#a78bfa',
                      borderRadius: '50%',
                      left: `${5 + Math.random() * 90}%`,
                      animation: `fall${i} ${2.5 + Math.random() * 3.5}s linear infinite`,
                      animationDelay: `${Math.random() * 5}s`,
                      opacity: 0,
                      boxShadow: '0 0 6px currentColor',
                      ['@keyframes fall' + i]: {
                        '0%': { 
                          transform: `translateY(-10px) translateX(0)`,
                          opacity: 0
                        },
                        '10%': { opacity: 0.9 },
                        '90%': { opacity: 0.9 },
                        '100%': { 
                          transform: `translateY(320px) translateX(${-15 + Math.random() * 30}px)`,
                          opacity: 0
                        }
                      }
                    }}
                  />
                ))}

                {/* Grid floor with perspective */}
                <Box sx={{
                  position: 'absolute',
                  bottom: 0,
                  left: 0,
                  right: 0,
                  height: '50%',
                  background: `
                    repeating-linear-gradient(0deg, 
                      transparent, 
                      transparent 29px, 
                      rgba(139, 92, 246, 0.4) 29px, 
                      rgba(139, 92, 246, 0.4) 30px
                    ),
                    repeating-linear-gradient(90deg, 
                      transparent, 
                      transparent 29px, 
                      rgba(139, 92, 246, 0.4) 29px, 
                      rgba(139, 92, 246, 0.4) 30px
                    )
                  `,
                  transform: 'perspective(400px) rotateX(65deg)',
                  transformOrigin: 'bottom center',
                  opacity: 0.8,
                  '&::after': {
                    content: '""',
                    position: 'absolute',
                    inset: 0,
                    background: 'linear-gradient(to top, rgba(167, 139, 250, 0.3), transparent 60%)',
                  }
                }} />

                {/* Main content */}
                <Box sx={{ position: 'relative', zIndex: 2, textAlign: 'center' }}>
                  <Typography 
                    variant="h3" 
                    sx={{ 
                      fontWeight: 800,
                      color: '#ffffff',
                      mb: 2,
                      textShadow: '0 0 50px rgba(255, 255, 255, 1), 0 0 100px rgba(167, 139, 250, 1), 0 5px 30px rgba(0, 0, 0, 1)',
                      letterSpacing: '0.02em',
                      fontSize: { xs: '1.5rem', sm: '2.2rem' }
                    }}
                  >
                    Cognix <span style={{ 
                      background: 'linear-gradient(135deg, #c4b5fd 0%, #93c5fd 100%)',
                      WebkitBackgroundClip: 'text',
                      WebkitTextFillColor: 'transparent',
                      backgroundClip: 'text',
                      filter: 'drop-shadow(0 0 40px rgba(167, 139, 250, 1)) drop-shadow(0 0 60px rgba(96, 165, 250, 1))'
                    }}>AI</span>
                  </Typography>

                  {/* 3D Brain Neural Network */}
                  <Box
                    sx={{
                      width: 180,
                      height: 180,
                      margin: '0 auto',
                      position: 'relative',
                      filter: 'drop-shadow(0 0 40px rgba(167, 139, 250, 1)) drop-shadow(0 0 70px rgba(96, 165, 250, 1))',
                    }}
                  >
                    <svg viewBox="0 0 200 200" style={{ width: '100%', height: '100%' }}>
                      <defs>
                        <linearGradient id="brainGradient" x1="0%" y1="0%" x2="100%" y2="100%">
                          <stop offset="0%" style={{ stopColor: '#c4b5fd', stopOpacity: 1 }} />
                          <stop offset="50%" style={{ stopColor: '#a78bfa', stopOpacity: 1 }} />
                          <stop offset="100%" style={{ stopColor: '#60a5fa', stopOpacity: 1 }} />
                        </linearGradient>
                        <filter id="brainGlow">
                          <feGaussianBlur stdDeviation="2.5" result="coloredBlur"/>
                          <feMerge>
                            <feMergeNode in="coloredBlur"/>
                            <feMergeNode in="coloredBlur"/>
                            <feMergeNode in="SourceGraphic"/>
                          </feMerge>
                        </filter>
                      </defs>

                      {/* Brain outline circle */}
                      <circle cx="100" cy="95" r="65" fill="none" stroke="url(#brainGradient)" strokeWidth="1.5" opacity="0.4" filter="url(#brainGlow)">
                        <animate attributeName="r" values="65;68;65" dur="4s" repeatCount="indefinite" />
                        <animate attributeName="opacity" values="0.3;0.5;0.3" dur="4s" repeatCount="indefinite" />
                      </circle>

                      {/* Dense neural network connections forming brain shape */}
                      <g stroke="url(#brainGradient)" strokeWidth="1" fill="none" opacity="0.8" filter="url(#brainGlow)">
                        {/* Top hemisphere connections */}
                        <line x1="100" y1="30" x2="70" y2="50"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.1s" repeatCount="indefinite" /></line>
                        <line x1="100" y1="30" x2="100" y2="55"><animate attributeName="opacity" values="0.5;1;0.5" dur="1.8s" repeatCount="indefinite" /></line>
                        <line x1="100" y1="30" x2="130" y2="50"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.3s" repeatCount="indefinite" /></line>
                        
                        {/* Upper left connections */}
                        <line x1="70" y1="50" x2="50" y2="65"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.5s" repeatCount="indefinite" /></line>
                        <line x1="70" y1="50" x2="60" y2="75"><animate attributeName="opacity" values="0.5;1;0.5" dur="1.9s" repeatCount="indefinite" /></line>
                        <line x1="70" y1="50" x2="85" y2="70"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.2s" repeatCount="indefinite" /></line>
                        
                        {/* Upper right connections */}
                        <line x1="130" y1="50" x2="150" y2="65"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.4s" repeatCount="indefinite" /></line>
                        <line x1="130" y1="50" x2="140" y2="75"><animate attributeName="opacity" values="0.5;1;0.5" dur="2s" repeatCount="indefinite" /></line>
                        <line x1="130" y1="50" x2="115" y2="70"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.6s" repeatCount="indefinite" /></line>
                        
                        {/* Center connections */}
                        <line x1="100" y1="55" x2="85" y2="70"><animate attributeName="opacity" values="0.5;1;0.5" dur="1.7s" repeatCount="indefinite" /></line>
                        <line x1="100" y1="55" x2="100" y2="80"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.3s" repeatCount="indefinite" /></line>
                        <line x1="100" y1="55" x2="115" y2="70"><animate attributeName="opacity" values="0.5;1;0.5" dur="1.9s" repeatCount="indefinite" /></line>
                        
                        {/* Mid-level connections */}
                        <line x1="50" y1="65" x2="40" y2="85"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.1s" repeatCount="indefinite" /></line>
                        <line x1="50" y1="65" x2="60" y2="90"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.4s" repeatCount="indefinite" /></line>
                        <line x1="60" y1="75" x2="75" y2="95"><animate attributeName="opacity" values="0.5;1;0.5" dur="1.8s" repeatCount="indefinite" /></line>
                        <line x1="85" y1="70" x2="75" y2="95"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.2s" repeatCount="indefinite" /></line>
                        <line x1="85" y1="70" x2="100" y2="95"><animate attributeName="opacity" values="0.5;1;0.5" dur="2s" repeatCount="indefinite" /></line>
                        
                        <line x1="150" y1="65" x2="160" y2="85"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.3s" repeatCount="indefinite" /></line>
                        <line x1="150" y1="65" x2="140" y2="90"><animate attributeName="opacity" values="0.5;1;0.5" dur="1.9s" repeatCount="indefinite" /></line>
                        <line x1="140" y1="75" x2="125" y2="95"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.5s" repeatCount="indefinite" /></line>
                        <line x1="115" y1="70" x2="125" y2="95"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.1s" repeatCount="indefinite" /></line>
                        <line x1="115" y1="70" x2="100" y2="95"><animate attributeName="opacity" values="0.5;1;0.5" dur="1.8s" repeatCount="indefinite" /></line>
                        
                        {/* Center hub connections */}
                        <line x1="100" y1="80" x2="75" y2="95"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.4s" repeatCount="indefinite" /></line>
                        <line x1="100" y1="80" x2="100" y2="95"><animate attributeName="opacity" values="0.5;1;0.5" dur="1.7s" repeatCount="indefinite" /></line>
                        <line x1="100" y1="80" x2="125" y2="95"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.2s" repeatCount="indefinite" /></line>
                        
                        {/* Lower connections */}
                        <line x1="40" y1="85" x2="50" y2="110"><animate attributeName="opacity" values="0.5;1;0.5" dur="2s" repeatCount="indefinite" /></line>
                        <line x1="60" y1="90" x2="70" y2="115"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.3s" repeatCount="indefinite" /></line>
                        <line x1="75" y1="95" x2="85" y2="120"><animate attributeName="opacity" values="0.5;1;0.5" dur="1.9s" repeatCount="indefinite" /></line>
                        <line x1="100" y1="95" x2="100" y2="125"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.1s" repeatCount="indefinite" /></line>
                        <line x1="125" y1="95" x2="115" y2="120"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.4s" repeatCount="indefinite" /></line>
                        <line x1="140" y1="90" x2="130" y2="115"><animate attributeName="opacity" values="0.5;1;0.5" dur="2s" repeatCount="indefinite" /></line>
                        <line x1="160" y1="85" x2="150" y2="110"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.5s" repeatCount="indefinite" /></line>
                        
                        {/* Bottom connections */}
                        <line x1="50" y1="110" x2="70" y2="130"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.2s" repeatCount="indefinite" /></line>
                        <line x1="70" y1="115" x2="85" y2="135"><animate attributeName="opacity" values="0.5;1;0.5" dur="1.8s" repeatCount="indefinite" /></line>
                        <line x1="85" y1="120" x2="100" y2="145"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.4s" repeatCount="indefinite" /></line>
                        <line x1="100" y1="125" x2="100" y2="145"><animate attributeName="opacity" values="0.5;1;0.5" dur="2s" repeatCount="indefinite" /></line>
                        <line x1="115" y1="120" x2="100" y2="145"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.3s" repeatCount="indefinite" /></line>
                        <line x1="130" y1="115" x2="115" y2="135"><animate attributeName="opacity" values="0.5;1;0.5" dur="1.9s" repeatCount="indefinite" /></line>
                        <line x1="150" y1="110" x2="130" y2="130"><animate attributeName="opacity" values="0.5;1;0.5" dur="2.1s" repeatCount="indefinite" /></line>
                      </g>

                      {/* Neural nodes forming brain shape */}
                      <g filter="url(#brainGlow)">
                        {/* Top nodes */}
                        <circle cx="100" cy="30" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2s" repeatCount="indefinite" /></circle>
                        
                        {/* Upper layer */}
                        <circle cx="70" cy="50" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.2s" repeatCount="indefinite" /></circle>
                        <circle cx="100" cy="55" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="1.8s" repeatCount="indefinite" /></circle>
                        <circle cx="130" cy="50" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.4s" repeatCount="indefinite" /></circle>
                        
                        {/* Mid-upper layer */}
                        <circle cx="50" cy="65" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.1s" repeatCount="indefinite" /></circle>
                        <circle cx="60" cy="75" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.3s" repeatCount="indefinite" /></circle>
                        <circle cx="85" cy="70" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="1.9s" repeatCount="indefinite" /></circle>
                        <circle cx="115" cy="70" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.5s" repeatCount="indefinite" /></circle>
                        <circle cx="140" cy="75" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2s" repeatCount="indefinite" /></circle>
                        <circle cx="150" cy="65" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.2s" repeatCount="indefinite" /></circle>
                        
                        {/* Center layer */}
                        <circle cx="40" cy="85" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.4s" repeatCount="indefinite" /></circle>
                        <circle cx="60" cy="90" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="1.8s" repeatCount="indefinite" /></circle>
                        <circle cx="75" cy="95" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.1s" repeatCount="indefinite" /></circle>
                        <circle cx="100" cy="80" r="4" fill="url(#brainGradient)"><animate attributeName="r" values="4;6;4" dur="1.7s" repeatCount="indefinite" /></circle>
                        <circle cx="100" cy="95" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.3s" repeatCount="indefinite" /></circle>
                        <circle cx="125" cy="95" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2s" repeatCount="indefinite" /></circle>
                        <circle cx="140" cy="90" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.2s" repeatCount="indefinite" /></circle>
                        <circle cx="160" cy="85" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.5s" repeatCount="indefinite" /></circle>
                        
                        {/* Lower layer */}
                        <circle cx="50" cy="110" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.1s" repeatCount="indefinite" /></circle>
                        <circle cx="70" cy="115" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="1.9s" repeatCount="indefinite" /></circle>
                        <circle cx="85" cy="120" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.4s" repeatCount="indefinite" /></circle>
                        <circle cx="100" cy="125" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2s" repeatCount="indefinite" /></circle>
                        <circle cx="115" cy="120" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.3s" repeatCount="indefinite" /></circle>
                        <circle cx="130" cy="115" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="1.8s" repeatCount="indefinite" /></circle>
                        <circle cx="150" cy="110" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.2s" repeatCount="indefinite" /></circle>
                        
                        {/* Bottom layer */}
                        <circle cx="70" cy="130" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.5s" repeatCount="indefinite" /></circle>
                        <circle cx="85" cy="135" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.1s" repeatCount="indefinite" /></circle>
                        <circle cx="100" cy="145" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="1.9s" repeatCount="indefinite" /></circle>
                        <circle cx="115" cy="135" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2.3s" repeatCount="indefinite" /></circle>
                        <circle cx="130" cy="130" r="3" fill="url(#brainGradient)"><animate attributeName="r" values="3;5;3" dur="2s" repeatCount="indefinite" /></circle>
                      </g>
                    </svg>
                  </Box>
                </Box>
              </Box>
            </Grid>
          </Grid>
        </Container>
      </Box>

      {/* Mission & Vision */}
      <Box sx={{ py: 8, backgroundColor: '#030303' }}>
        <Container maxWidth="lg">
          <Grid container spacing={4}>
            <Grid item xs={12} md={6}>
              <Paper sx={{ p: 4, height: '100%', textAlign: 'center', backgroundColor: 'rgba(255,255,255,0.06)', border: '1px solid rgba(255,255,255,0.08)' }}>
                <Typography variant="h4" gutterBottom sx={{ color: '#8b5cf6' }}>
                  Our Mission
                </Typography>
                <Typography variant="body1" sx={{ color: '#d1d5db' }}>
                  To help businesses grow and innovate by delivering intelligent, reliable, 
                  and data-driven digital solutions.
                </Typography>
              </Paper>
            </Grid>
            <Grid item xs={12} md={6}>
              <Paper sx={{ p: 4, height: '100%', textAlign: 'center', backgroundColor: 'rgba(255,255,255,0.06)', border: '1px solid rgba(255,255,255,0.08)' }}>
                <Typography variant="h4" gutterBottom sx={{ color: '#8b5cf6' }}>
                  Our Vision
                </Typography>
                <Typography variant="body1" sx={{ color: '#d1d5db' }}>
                  To become a globally trusted AI-first technology partner for startups, 
                  enterprises, and innovation-driven organizations.
                </Typography>
              </Paper>
            </Grid>
          </Grid>
        </Container>
      </Box>

      {/* Our Values */}
      <Box sx={{ py: 8, backgroundColor: '#050505' }}>
        <Container maxWidth="lg">
          <Typography variant="h3" textAlign="center" gutterBottom sx={{ color: 'white', fontWeight: 'bold' }}>
            Our Values
          </Typography>
          <Grid container spacing={4} sx={{ mt: 2 }}>
            {values.map((value, index) => (
              <Grid item xs={12} md={6} key={index}>
                <Card sx={{ height: '100%', p: 2, backgroundColor: 'rgba(255,255,255,0.04)', border: '1px solid rgba(255,255,255,0.08)' }}>
                  <CardContent sx={{ textAlign: 'center' }}>
                    {value.icon}
                    <Typography variant="h5" sx={{ mt: 2, mb: 2, fontWeight: 'bold', color: 'white' }}>
                      {value.title}
                    </Typography>
                    <Typography variant="body1" sx={{ color: '#d1d5db' }}>
                      {value.description}
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>

      {/* Founders */}
      <Box sx={{ py: 8, backgroundColor: '#030303' }}>
        <Container maxWidth="lg">
          <Typography variant="h3" textAlign="center" gutterBottom sx={{ color: 'white', fontWeight: 'bold' }}>
            Our Founders
          </Typography>
          <Grid container spacing={4} sx={{ mt: 2 }}>
            {founders.map((founder, index) => (
              <Grid item xs={12} md={6} key={index}>
                <Card sx={{ height: '100%', backgroundColor: 'rgba(255,255,255,0.04)', border: '1px solid rgba(255,255,255,0.08)' }}>
                  <CardContent sx={{ textAlign: 'center', p: 4 }}>
                    <Avatar
                      sx={{
                        width: 120,
                        height: 120,
                        mx: 'auto',
                        mb: 3,
                        backgroundColor: 'primary.main',
                        fontSize: '2rem'
                      }}
                    >
                      {founder.name.split(' ').map(n => n[0]).join('')}
                    </Avatar>
                    <Typography variant="h5" gutterBottom fontWeight="bold" sx={{ color: 'white' }}>
                      {founder.name}
                    </Typography>
                    <Typography variant="subtitle1" sx={{ color: '#8b5cf6' }} gutterBottom>
                      {founder.position}
                    </Typography>
                    <Typography variant="body2" sx={{ color: '#9ca3af' }} gutterBottom>
                      {founder.education}
                    </Typography>
                    <Typography variant="body1" sx={{ mt: 2, color: '#d1d5db' }}>
                      {founder.description}
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>

      {/* CTA Section */}
      <Box
        sx={{
          background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
          color: 'white',
          py: 8,
          textAlign: 'center'
        }}
      >
        <Container maxWidth="lg">
          <Typography variant="h3" gutterBottom>
            Ready to Work Together?
          </Typography>
          <Typography variant="h6" sx={{ mb: 2, opacity: 0.9 }}>
            Let's discuss how we can help transform your business with intelligent technology solutions.
          </Typography>
          <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', gap: 1, mb: 1 }}>
            <Email sx={{ fontSize: '1.3rem' }} />
            <Typography variant="body1" sx={{ fontSize: '1.1rem' }}>
              cognixpvtltb@gmail.com
            </Typography>
          </Box>
          <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', gap: 1 }}>
            <WhatsApp sx={{ fontSize: '1.3rem' }} />
            <Typography variant="body1" sx={{ fontSize: '1.1rem' }}>
              +91 7991955775
            </Typography>
          </Box>
        </Container>
      </Box>
    </Box>
  );
};

export default About;