import React from 'react';
import { Box, Container, Typography, Grid, Chip } from '@mui/material';
import { motion } from 'framer-motion';

const DarkCaseStudies = () => {
  const studies = [
    {
      title: "E-Commerce Growth",
      tag: "Retail", 
      result: "300% Growth"
    },
    {
      title: "AI Support Chatbot",
      tag: "SaaS",
      result: "60% Workload Reduction"
    },
    {
      title: "Enterprise Web Platform", 
      tag: "Finance",
      result: "High-Performance"
    }
  ];

  const getTagColor = (tag) => {
    const colors = {
      'Retail': '#10b981',
      'SaaS': '#3b82f6', 
      'Finance': '#f59e0b'
    };
    return colors[tag] || '#8b5cf6';
  };

  return (
    <Box
      sx={{
        py: 12,
        backgroundColor: '#050505'
      }}
    >
      <Container maxWidth="lg" sx={{ px: 3 }}>
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          viewport={{ once: true }}
        >
          <Typography
            variant="h2"
            sx={{
              fontSize: { xs: '2.5rem', md: '3rem' },
              fontWeight: 'bold',
              color: '#f3f4f6',
              textAlign: 'center',
              mb: 6
            }}
          >
            Selected Case Studies
          </Typography>
        </motion.div>

        <Grid container spacing={4}>
          {studies.map((study, index) => (
            <Grid item xs={12} md={4} key={index}>
              <motion.div
                initial={{ opacity: 0, y: 30 }}
                whileInView={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, delay: index * 0.1 }}
                viewport={{ once: true }}
                whileHover={{ 
                  scale: 1.02,
                  transition: { duration: 0.2 }
                }}
              >
                <Box
                  sx={{
                    borderRadius: 6,
                    overflow: 'hidden',
                    backgroundColor: '#0a0a0a',
                    border: '1px solid rgba(255, 255, 255, 0.05)',
                    transition: 'transform 0.3s ease-in-out',
                    height: '100%'
                  }}
                >
                  {/* Project Preview */}
                  <Box
                    sx={{
                      height: '192px',
                      background: 'linear-gradient(135deg, rgba(88, 28, 135, 0.4) 0%, rgba(30, 58, 138, 0.4) 100%)',
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center'
                    }}
                  >
                    <Typography
                      sx={{
                        color: 'rgba(255, 255, 255, 0.6)',
                        fontWeight: 'bold',
                        fontSize: '1.125rem'
                      }}
                    >
                      Project Preview
                    </Typography>
                  </Box>

                  {/* Content */}
                  <Box sx={{ p: 3 }}>
                    <Chip
                      label={study.tag}
                      sx={{
                        px: 1.5,
                        py: 0.5,
                        fontSize: '0.8rem',
                        fontWeight: 'bold',
                        backgroundColor: `${getTagColor(study.tag)}25`,
                        color: getTagColor(study.tag),
                        border: `1.5px solid ${getTagColor(study.tag)}60`,
                        mb: 2
                      }}
                    />
                    <Typography
                      variant="h5"
                      sx={{
                        fontWeight: 'bold',
                        color: '#ffffff',
                        mb: 1.5,
                        fontSize: '1.35rem'
                      }}
                    >
                      {study.title}
                    </Typography>
                    <Typography
                      sx={{
                        color: getTagColor(study.tag),
                        fontWeight: 'bold',
                        display: 'flex',
                        alignItems: 'center',
                        gap: 0.5,
                        fontSize: '1rem'
                      }}
                    >
                       {study.result}
                    </Typography>
                  </Box>
                </Box>
              </motion.div>
            </Grid>
          ))}
        </Grid>
      </Container>
    </Box>
  );
};

export default DarkCaseStudies;