import React from 'react';
import { Box, Container, Typography, Grid } from '@mui/material';
import { motion } from 'framer-motion';

const DarkExpertise = () => {
  const services = [
    {
      title: "SEO, AEO & GEO Optimization",
      icon: "",
      desc: "We optimize your digital presence for traditional search engines and AI-powered answer engines."
    },
    {
      title: "Web Development", 
      icon: "",
      desc: "Fast, secure, and SEO-optimized websites that convert visitors into loyal customers."
    },
    {
      title: "App Development",
      icon: "", 
      desc: "Scalable mobile applications tailored to unique business requirements."
    },
    {
      title: "AI & Intelligent Solutions",
      icon: "",
      desc: "Custom chatbots, automation systems, and generative AI applications."
    }
  ];

  return (
    <Box
      sx={{
        py: 12,
        backgroundColor: '#080808',
        color: 'white'
      }}
    >
      <Container maxWidth="lg" sx={{ px: 3 }}>
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          viewport={{ once: true }}
        >
          <Typography
            variant="h2"
            sx={{
              fontSize: { xs: '2.5rem', md: '3rem' },
              fontWeight: 'bold',
              textAlign: 'center',
              color: '#e5e7eb',
              mb: 8
            }}
          >
            Our Core{' '}
            <Box component="span" sx={{ color: '#8b5cf6' }}>
              Expertise
            </Box>
          </Typography>
        </motion.div>

        <Grid container spacing={3}>
          {services.map((item, index) => (
            <Grid item xs={12} md={6} key={index}>
              <motion.div
                initial={{ opacity: 0, y: 30 }}
                whileInView={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, delay: index * 0.1 }}
                viewport={{ once: true }}
                whileHover={{ 
                  scale: 1.02,
                  transition: { duration: 0.2 }
                }}
              >
                <Box
                  sx={{
                    p: 4,
                    borderRadius: 4,
                    background: '#121212',
                    border: '1px solid rgba(255, 255, 255, 0.1)',
                    transition: 'all 0.3s ease-in-out',
                    height: '100%',
                    '&:hover': {
                      borderColor: '#a855f7',
                      boxShadow: '0 0 20px rgba(168, 85, 247, 0.4), 0 0 40px rgba(168, 85, 247, 0.2)',
                      transform: 'translateY(-4px)',
                      '& .service-icon': {
                        transform: 'scale(1.1)',
                      }
                    }
                  }}
                >
                  <Box
                    className="service-icon"
                    sx={{
                      fontSize: '3rem',
                      mb: 2,
                      transition: 'transform 0.3s ease-in-out',
                      display: 'block'
                    }}
                  >
                    {item.icon}
                  </Box>
                  <Typography
                    variant="h5"
                    sx={{
                      fontWeight: 'bold',
                      mb: 1.5,
                      fontSize: '1.25rem',
                      color: '#f8fafc'
                    }}
                  >
                    {item.title}
                  </Typography>
                  <Typography
                    sx={{
                      color: '#d1d5db',
                      lineHeight: 1.6
                    }}
                  >
                    {item.desc}
                  </Typography>
                </Box>
              </motion.div>
            </Grid>
          ))}
        </Grid>
      </Container>
    </Box>
  );
};

export default DarkExpertise;