import React from 'react';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Stepper,
  Step,
  StepLabel,
  StepConnector,
  Paper
} from '@mui/material';
import {
  TrendingUp,
  Code,
  Phone,
  SmartToy,
  CheckCircle,
  Search,
  Web,
  Android,
  Psychology,
  Email,
  WhatsApp
} from '@mui/icons-material';

const Services = () => {
  const services = [
    {
      icon: '',
      title: 'SEO, AEO & GEO Services',
      description: 'Our SEO services are designed to improve visibility across both traditional search engines and AI-driven answer platforms.',
      features: [
        'Technical SEO audits and optimization',
        'On-page and off-page SEO',
        'Local SEO and Google Business optimization',
        'Answer Engine Optimization (AEO)',
        'Generative Engine Optimization (GEO)',
        'AI-search visibility strategies'
      ]
    },
    {
      icon: '',
      title: 'Web Development',
      description: 'We build modern, secure, and scalable web solutions aligned with your business goals.',
      features: [
        'Business and corporate websites',
        'SaaS and startup platforms',
        'SEO-optimized landing pages',
        'CMS and headless solutions',
        'E-commerce platforms',
        'Progressive Web Applications (PWA)'
      ]
    },
    {
      icon: '',
      title: 'App Development',
      description: 'We create mobile applications that are reliable, user-friendly, and scalable.',
      features: [
        'Android and iOS development',
        'Cross-platform solutions',
        'Enterprise mobile applications',
        'UI/UX design and optimization',
        'App store optimization',
        'Maintenance and support'
      ]
    },
    {
      icon: '',
      title: 'AI & Automation',
      description: 'We design intelligent systems that automate processes and enhance decision-making.',
      features: [
        'AI-powered chatbots',
        'Computer vision solutions',
        'Generative AI applications',
        'Workflow automation systems',
        'Custom AI model development',
        'Natural Language Processing (NLP)'
      ]
    }
  ];

  const processSteps = [
    'Requirement analysis and discovery',
    'Strategy and solution planning',
    'Design and development',
    'Testing and deployment',
    'Continuous optimization and support'
  ];

  const technologies = [
    { category: 'Frontend', items: ['React', 'Vue.js', 'Angular', 'Next.js', 'TypeScript'] },
    { category: 'Backend', items: ['Node.js', 'Python', 'Java', 'PHP', 'MongoDB', 'PostgreSQL'] },
    { category: 'Mobile', items: ['React Native', 'Flutter', 'Swift', 'Kotlin'] },
    { category: 'AI/ML', items: ['TensorFlow', 'PyTorch', 'OpenAI', 'Hugging Face', 'LangChain'] },
    { category: 'Cloud', items: ['AWS', 'Google Cloud', 'Azure', 'Docker', 'Kubernetes'] },
    { category: 'SEO Tools', items: ['Google Analytics', 'Search Console', 'SEMrush', 'Ahrefs'] }
  ];

  return (
    <Box sx={{ backgroundColor: '#030303' }}>
      {/* Hero Section */}
      <Box
        sx={{
          background: 'linear-gradient(135deg, rgba(88, 28, 135, 0.9) 0%, rgba(30, 58, 138, 0.9) 100%)',
          color: 'white',
          py: { xs: 7, md: 9 },
          textAlign: 'center',
          borderBottom: '1px solid rgba(255,255,255,0.06)'
        }}
      >
        <Container maxWidth="lg">
          <Typography variant="h2" component="h1" gutterBottom sx={{ fontWeight: 'bold', color: '#ffffff' }}>
            Our Services
          </Typography>
          <Typography variant="h5" sx={{ opacity: 0.95, color: '#e5e7eb' }}>
            Comprehensive digital solutions powered by AI and modern technology
          </Typography>
        </Container>
      </Box>

      {/* Services Grid */}
      <Box sx={{ py: 8, backgroundColor: '#050505' }}>
        <Container maxWidth="lg">
          <Grid container spacing={4}>
            {services.map((service, index) => (
              <Grid item xs={12} md={6} key={index}>
                <Card sx={{ height: '100%', p: 2, backgroundColor: 'rgba(255,255,255,0.04)', border: '1px solid rgba(255,255,255,0.08)' }}>
                  <CardContent>
                    <Box sx={{ textAlign: 'center', mb: 3 }}>
                      <Box sx={{ fontSize: 48, lineHeight: 1, mb: 1 }}>
                        {service.icon}
                      </Box>
                      <Typography variant="h4" sx={{ mt: 2, mb: 2, fontWeight: 'bold', color: 'white' }}>
                        {service.title}
                      </Typography>
                      <Typography variant="body1" sx={{ color: '#d1d5db' }}>
                        {service.description}
                      </Typography>
                    </Box>
                    <List>
                      {service.features.map((feature, idx) => (
                        <ListItem key={idx} sx={{ py: 0.5 }}>
                          <ListItemIcon>
                            <CheckCircle sx={{ color: '#8b5cf6' }} />
                          </ListItemIcon>
                          <ListItemText primary={feature} primaryTypographyProps={{ sx: { color: '#e5e7eb' } }} />
                        </ListItem>
                      ))}
                    </List>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>

      {/* Our Process */}
      <Box sx={{ py: 8, backgroundColor: '#030303' }}>
        <Container maxWidth="lg">
          <Typography variant="h3" textAlign="center" gutterBottom sx={{ color: 'white', fontWeight: 'bold' }}>
            Our Process
          </Typography>
          <Typography variant="h6" textAlign="center" sx={{ mb: 6, opacity: 0.9, color: '#cbd5e1' }}>
            A systematic approach to deliver exceptional results
          </Typography>
          <Box
            sx={{
              maxWidth: 760,
              mx: 'auto',
              px: { xs: 2, sm: 4 },
              py: { xs: 3, sm: 4 },
              borderRadius: 4,
              backgroundColor: 'rgba(255,255,255,0.04)',
              border: '1px solid rgba(255,255,255,0.08)'
            }}
          >
            <Stepper
              orientation="vertical"
              connector={
                <StepConnector
                  sx={{
                    '& .MuiStepConnector-line': {
                      borderColor: 'rgba(255,255,255,0.25)',
                      borderLeftWidth: 2,
                      minHeight: 24
                    }
                  }}
                />
              }
              sx={{
                '& .MuiStep-root': { py: 0.5 },
                '& .MuiStepLabel-root': { alignItems: 'flex-start' },
                '& .MuiStepLabel-label': { mt: 0.5 }
              }}
            >
              {processSteps.map((step, index) => (
                <Step key={index} active={true}>
                  <StepLabel
                    sx={{
                      '& .MuiStepLabel-label': { color: '#e5e7eb' },
                      '& .MuiStepLabel-iconContainer .MuiStepIcon-root': { color: '#8b5cf6' },
                      '& .MuiStepLabel-iconContainer .MuiStepIcon-text': { fill: '#0b0b0b', fontWeight: 700 }
                    }}
                  >
                    <Typography variant="h6" fontWeight="bold" sx={{ color: '#e5e7eb' }}>
                      {step}
                    </Typography>
                  </StepLabel>
                </Step>
              ))}
            </Stepper>
          </Box>
        </Container>
      </Box>

      {/* Technologies */}
      <Box sx={{ py: 8, backgroundColor: '#050505' }}>
        <Container maxWidth="lg">
          <Typography variant="h3" textAlign="center" gutterBottom sx={{ color: 'white', fontWeight: 'bold' }}>
            Technologies We Use
          </Typography>
          <Typography variant="h6" textAlign="center" sx={{ mb: 6, opacity: 0.9, color: '#cbd5e1' }}>
            Cutting-edge tools and frameworks for modern solutions
          </Typography>
          <Grid container spacing={3}>
            {technologies.map((tech, index) => (
              <Grid item xs={12} md={6} lg={4} key={index}>
                <Paper sx={{ p: 3, height: '100%', backgroundColor: 'rgba(255,255,255,0.04)', border: '1px solid rgba(255,255,255,0.08)' }}>
                  <Typography variant="h5" gutterBottom fontWeight="bold" sx={{ color: '#8b5cf6' }}>
                    {tech.category}
                  </Typography>
                  <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
                    {tech.items.map((item, idx) => (
                      <Box
                        key={idx}
                        sx={{
                          backgroundColor: 'rgba(255,255,255,0.08)',
                          px: 2,
                          py: 0.5,
                          borderRadius: 1,
                          fontSize: '0.875rem',
                          color: '#e5e7eb'
                        }}
                      >
                        {item}
                      </Box>
                    ))}
                  </Box>
                </Paper>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>

      {/* Service Icons Grid */}
      <Box sx={{ py: 8, backgroundColor: '#030303' }}>
        <Container maxWidth="lg">
          <Typography variant="h3" textAlign="center" gutterBottom sx={{ color: 'white', fontWeight: 'bold' }}>
            What We Deliver
          </Typography>
          <Typography variant="h6" textAlign="center" sx={{ mb: 6, opacity: 0.9, color: '#cbd5e1' }}>
            Focused outcomes that help your business grow faster
          </Typography>
          <Grid container spacing={4} sx={{ mt: 2 }}>
            {[
              { icon: '', title: 'SEO Optimization', desc: 'Improved search rankings' },
              { icon: '', title: 'Web Solutions', desc: 'Modern, responsive websites' },
              { icon: '', title: 'Mobile Apps', desc: 'Cross-platform applications' },
              { icon: '', title: 'AI Integration', desc: 'Intelligent automation' }
            ].map((item, index) => (
              <Grid item xs={12} sm={6} md={3} key={index}>
                <Paper
                  sx={{
                    p: 3,
                    textAlign: 'center',
                    height: '100%',
                    backgroundColor: 'rgba(255,255,255,0.04)',
                    border: '1px solid rgba(255,255,255,0.08)',
                    borderRadius: 3,
                    transition: 'all 0.3s ease-in-out',
                    '&:hover': {
                      transform: 'translateY(-4px)',
                      borderColor: 'rgba(139,92,246,0.5)'
                    }
                  }}
                >
                  <Box sx={{ fontSize: 36, mb: 2 }}>
                    {item.icon}
                  </Box>
                  <Typography variant="h6" gutterBottom fontWeight="bold" sx={{ color: 'white' }}>
                    {item.title}
                  </Typography>
                  <Typography variant="body2" sx={{ color: '#d1d5db' }}>
                    {item.desc}
                  </Typography>
                </Paper>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>

      {/* CTA Section */}
      <Box
        sx={{
          background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
          color: 'white',
          py: 8,
          textAlign: 'center'
        }}
      >
        <Container maxWidth="lg">
          <Typography variant="h3" gutterBottom>
            Ready to Get Started?
          </Typography>
          <Typography variant="h6" sx={{ mb: 2, opacity: 0.9 }}>
            Let's discuss your project requirements and create a custom solution for your business.
          </Typography>
          <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', gap: 1, mb: 1 }}>
            <Email sx={{ fontSize: '1.3rem' }} />
            <Typography variant="body1" sx={{ fontSize: '1.1rem' }}>
              cognixpvtltb@gmail.com
            </Typography>
          </Box>
          <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', gap: 1 }}>
            <WhatsApp sx={{ fontSize: '1.3rem' }} />
            <Typography variant="body1" sx={{ fontSize: '1.1rem' }}>
              +91 7991955775
            </Typography>
          </Box>
        </Container>
      </Box>
    </Box>
  );
};

export default Services;