import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  Rating,
  Avatar,
  Paper
} from '@mui/material';
import { FormatQuote } from '@mui/icons-material';
import axios from 'axios';

const Testimonials = () => {
  const [testimonials, setTestimonials] = useState([]);

  useEffect(() => {
    // Fetch testimonials
    axios.get('/api/testimonials')
      .then(response => setTestimonials(response.data))
      .catch(error => {
        console.error('Error fetching testimonials:', error);
        // Fallback to static data if API fails
        setTestimonials([
          {
            _id: '1',
            name: 'Amit Sharma',
            position: 'Founder',
            company: 'TechNova Solutions',
            message: 'Cognix AI helped us significantly improve our search visibility and overall digital strategy. Their AI-first approach delivered measurable results.',
            rating: 5
          },
          {
            _id: '2',
            name: 'Neha Verma',
            position: 'Product Manager',
            company: 'SaaSFlow',
            message: 'The AI chatbot developed by Cognix AI streamlined our customer support operations and reduced response time significantly.',
            rating: 5
          },
          {
            _id: '3',
            name: 'Rahul Mehta',
            position: 'Director',
            company: 'FinEdge Corp',
            message: 'A reliable and professional team with strong technical capabilities. They delivered our project on time and exceeded expectations.',
            rating: 5
          },
          {
            _id: '4',
            name: 'Priya Singh',
            position: 'Marketing Head',
            company: 'EcoTech Solutions',
            message: 'Their SEO expertise helped us achieve 250% growth in organic traffic within 6 months. Highly recommended!',
            rating: 5
          },
          {
            _id: '5',
            name: 'Vikash Kumar',
            position: 'CTO',
            company: 'DataFlow Systems',
            message: 'Cognix AI built a robust web platform that handles our complex data processing needs efficiently.',
            rating: 5
          },
          {
            _id: '6',
            name: 'Anjali Gupta',
            position: 'CEO',
            company: 'HealthTech Innovations',
            message: 'Their AI automation solutions transformed our workflow and improved our operational efficiency by 40%.',
            rating: 5
          }
        ]);
      });
  }, []);

  const getInitials = (name) => {
    return name.split(' ').map(n => n[0]).join('').toUpperCase();
  };

  const getAvatarColor = (index) => {
    const colors = ['primary.main', 'secondary.main', 'success.main', 'warning.main', 'error.main', 'info.main'];
    return colors[index % colors.length];
  };

  return (
    <Box sx={{ backgroundColor: '#030303' }}>
      {/* Hero Section */}
      <Box
        sx={{
          background: 'linear-gradient(135deg, rgba(88, 28, 135, 0.9) 0%, rgba(30, 58, 138, 0.9) 100%)',
          color: 'white',
          py: { xs: 7, md: 9 },
          textAlign: 'center',
          borderBottom: '1px solid rgba(255,255,255,0.06)'
        }}
      >
        <Container maxWidth="lg">
          <Typography variant="h2" component="h1" gutterBottom sx={{ fontWeight: 'bold', color: '#ffffff' }}>
            Client Testimonials
          </Typography>
          <Typography variant="h5" sx={{ opacity: 0.95, color: '#e5e7eb' }}>
            What our clients say about working with Cognix AI
          </Typography>
        </Container>
      </Box>

      {/* Testimonials Grid */}
      <Box sx={{ py: 8, backgroundColor: '#050505' }}>
        <Container maxWidth="lg">
          <Typography variant="h3" textAlign="center" gutterBottom sx={{ color: 'white', fontWeight: 'bold' }}>
            Success Stories
          </Typography>
          <Typography variant="h6" textAlign="center" sx={{ mb: 6, opacity: 0.9, color: '#cbd5e1' }}>
            Real feedback from real clients
          </Typography>

          <Grid container spacing={4}>
            {testimonials.map((testimonial, index) => (
              <Grid item xs={12} md={6} lg={4} key={testimonial._id}>
                <Card 
                  sx={{ 
                    height: '100%',
                    position: 'relative',
                    transition: 'all 0.4s ease',
                    backgroundColor: 'rgba(15,15,25,0.7)',
                    border: '2px solid transparent',
                    backgroundImage: 'linear-gradient(rgba(15,15,25,0.9), rgba(15,15,25,0.9)), linear-gradient(135deg, #8b5cf6, #3b82f6, #8b5cf6)',
                    backgroundOrigin: 'border-box',
                    backgroundClip: 'padding-box, border-box',
                    backdropFilter: 'blur(10px)',
                    borderRadius: 2,
                    overflow: 'hidden',
                    animation: `float${index % 3} 6s ease-in-out infinite`,
                    animationDelay: `${index * 0.2}s`,
                    '@keyframes float0': {
                      '0%, 100%': { transform: 'translateY(0px)' },
                      '50%': { transform: 'translateY(-8px)' }
                    },
                    '@keyframes float1': {
                      '0%, 100%': { transform: 'translateY(0px) rotate(0deg)' },
                      '50%': { transform: 'translateY(-10px) rotate(1deg)' }
                    },
                    '@keyframes float2': {
                      '0%, 100%': { transform: 'translateY(0px)' },
                      '50%': { transform: 'translateY(-6px)' }
                    },
                    '&::before': {
                      content: '""',
                      position: 'absolute',
                      top: 0,
                      left: 0,
                      right: 0,
                      bottom: 0,
                      background: 'radial-gradient(circle at 50% 0%, rgba(139, 92, 246, 0.15), transparent 70%)',
                      opacity: 0,
                      transition: 'opacity 0.4s ease',
                      pointerEvents: 'none',
                      zIndex: 0
                    },
                    '&:hover': {
                      transform: 'translateY(-12px) scale(1.02)',
                      boxShadow: '0 20px 60px rgba(139, 92, 246, 0.4), 0 0 40px rgba(59, 130, 246, 0.3)',
                      '&::before': {
                        opacity: 1
                      }
                    }
                  }}
                >
                  <CardContent sx={{ p: 3, position: 'relative', zIndex: 1 }}>
                    {/* Quote Icon */}
                    <FormatQuote 
                      sx={{ 
                        fontSize: 48, 
                        color: '#a78bfa', 
                        opacity: 0.4,
                        position: 'absolute',
                        top: 16,
                        right: 16,
                        animation: 'pulse 3s ease-in-out infinite',
                        filter: 'drop-shadow(0 0 8px rgba(167, 139, 250, 0.6))',
                        '@keyframes pulse': {
                          '0%, 100%': { opacity: 0.4, transform: 'scale(1)' },
                          '50%': { opacity: 0.7, transform: 'scale(1.1)' }
                        }
                      }} 
                    />

                    {/* Rating */}
                    <Rating 
                      value={testimonial.rating} 
                      readOnly 
                      sx={{ 
                        mb: 2,
                        '& .MuiRating-iconFilled': {
                          color: '#fbbf24',
                          filter: 'drop-shadow(0 0 4px rgba(251, 191, 36, 0.6))'
                        },
                        '& .MuiRating-icon': {
                          transition: 'all 0.3s ease'
                        }
                      }}
                    />

                    {/* Testimonial Text */}
                    <Typography 
                      variant="body1" 
                      sx={{ 
                        mb: 3, 
                        fontStyle: 'italic',
                        lineHeight: 1.6,
                        color: '#d1d5db'
                      }}
                    >
                      "{testimonial.message}"
                    </Typography>

                    {/* Client Info */}
                    <Box sx={{ display: 'flex', alignItems: 'center', mt: 'auto' }}>
                      <Avatar 
                        sx={{ 
                          background: 'linear-gradient(135deg, #8b5cf6, #3b82f6)',
                          mr: 2,
                          width: 56,
                          height: 56,
                          fontSize: '1.25rem',
                          fontWeight: 700,
                          border: '2px solid rgba(139, 92, 246, 0.3)',
                          boxShadow: '0 0 20px rgba(139, 92, 246, 0.4)',
                          transition: 'all 0.3s ease',
                          '&:hover': {
                            transform: 'scale(1.1)',
                            boxShadow: '0 0 30px rgba(139, 92, 246, 0.7)'
                          }
                        }}
                      >
                        {getInitials(testimonial.name)}
                      </Avatar>
                      <Box>
                        <Typography variant="subtitle1" fontWeight="bold" sx={{ color: 'white' }}>
                          {testimonial.name}
                        </Typography>
                        <Typography variant="body2" sx={{ color: '#9ca3af' }}>
                          {testimonial.position}
                        </Typography>
                        <Typography variant="body2" sx={{ color: '#8b5cf6' }} fontWeight="medium">
                          {testimonial.company}
                        </Typography>
                      </Box>
                    </Box>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>

          {testimonials.length === 0 && (
            <Box sx={{ textAlign: 'center', py: 8 }}>
              <Typography variant="h6" sx={{ color: '#9ca3af' }}>
                Loading testimonials...
              </Typography>
            </Box>
          )}
        </Container>
      </Box>

      {/* Stats Section */}
      <Box sx={{ py: 8, backgroundColor: '#030303', position: 'relative', overflow: 'hidden' }}>
        {/* Animated background particles */}
        <Box
          sx={{
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            opacity: 0.1,
            background: 'radial-gradient(circle at 20% 50%, rgba(139, 92, 246, 0.3), transparent 40%), radial-gradient(circle at 80% 50%, rgba(59, 130, 246, 0.3), transparent 40%)'
          }}
        />
        <Container maxWidth="lg" sx={{ position: 'relative', zIndex: 1 }}>
          <Typography 
            variant="h3" 
            textAlign="center" 
            gutterBottom 
            sx={{ 
              color: 'white', 
              fontWeight: 'bold',
              background: 'linear-gradient(135deg, #ffffff, #c4b5fd)',
              WebkitBackgroundClip: 'text',
              WebkitTextFillColor: 'transparent',
              backgroundClip: 'text',
              mb: 6
            }}
          >
            Our Impact
          </Typography>
          <Grid container spacing={4} sx={{ mt: 2 }}>
            {[
              { number: '50+', label: 'Projects Completed' },
              { number: '30+', label: 'Happy Clients' },
              { number: '95%', label: 'Client Satisfaction' },
              { number: '24/7', label: 'Support Available' }
            ].map((stat, index) => (
              <Grid item xs={12} sm={6} md={3} key={index}>
                <Paper 
                  sx={{ 
                    p: 4, 
                    textAlign: 'center', 
                    height: '100%', 
                    backgroundColor: 'rgba(15,15,25,0.8)',
                    border: '2px solid transparent',
                    backgroundImage: 'linear-gradient(rgba(15,15,25,0.9), rgba(15,15,25,0.9)), linear-gradient(135deg, #8b5cf6, #3b82f6)',
                    backgroundOrigin: 'border-box',
                    backgroundClip: 'padding-box, border-box',
                    borderRadius: 2,
                    position: 'relative',
                    overflow: 'hidden',
                    transition: 'all 0.4s ease',
                    animation: `fadeInUp 0.6s ease-out ${index * 0.15}s backwards`,
                    '@keyframes fadeInUp': {
                      '0%': { opacity: 0, transform: 'translateY(40px)' },
                      '100%': { opacity: 1, transform: 'translateY(0)' }
                    },
                    '&::before': {
                      content: '""',
                      position: 'absolute',
                      top: '-50%',
                      left: '-50%',
                      width: '200%',
                      height: '200%',
                      background: 'conic-gradient(from 0deg at 50% 50%, transparent, rgba(139, 92, 246, 0.15), transparent 30%)',
                      animation: 'rotate 8s linear infinite',
                      opacity: 0
                    },
                    '@keyframes rotate': {
                      '100%': { transform: 'rotate(360deg)' }
                    },
                    '&:hover': {
                      transform: 'translateY(-8px) scale(1.03)',
                      boxShadow: '0 20px 50px rgba(139, 92, 246, 0.5)',
                      '&::before': {
                        opacity: 1
                      }
                    }
                  }}
                >
                  <Typography 
                    variant="h2" 
                    sx={{ 
                      background: 'linear-gradient(135deg, #c4b5fd, #60a5fa)',
                      WebkitBackgroundClip: 'text',
                      WebkitTextFillColor: 'transparent',
                      backgroundClip: 'text',
                      position: 'relative',
                      zIndex: 1,
                      filter: 'drop-shadow(0 0 20px rgba(139, 92, 246, 0.6))'
                    }}
                    fontWeight="bold"
                    gutterBottom
                  >
                    {stat.number}
                  </Typography>
                  <Typography variant="h6" sx={{ color: '#e5e7eb', position: 'relative', zIndex: 1, fontWeight: 500 }}>
                    {stat.label}
                  </Typography>
                </Paper>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>

      {/* Client Logos Section */}
      <Box sx={{ py: 8, backgroundColor: '#050505' }}>
        <Container maxWidth="lg">
          <Typography 
            variant="h3" 
            textAlign="center" 
            gutterBottom 
            sx={{ 
              color: 'white', 
              fontWeight: 'bold',
              background: 'linear-gradient(135deg, #ffffff, #c4b5fd)',
              WebkitBackgroundClip: 'text',
              WebkitTextFillColor: 'transparent',
              backgroundClip: 'text',
              mb: 6
            }}
          >
            Trusted By
          </Typography>
          <Grid container spacing={3} sx={{ mt: 2 }}>
            {[
              'TechNova Solutions',
              'SaaSFlow',
              'FinEdge Corp',
              'EcoTech Solutions',
              'DataFlow Systems',
              'HealthTech Innovations'
            ].map((company, index) => (
              <Grid item xs={12} sm={6} md={4} key={index}>
                <Paper 
                  sx={{ 
                    p: 3, 
                    textAlign: 'center',
                    height: 100,
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    backgroundColor: 'rgba(15,15,25,0.6)',
                    border: '2px solid rgba(139, 92, 246, 0.2)',
                    borderRadius: 2,
                    transition: 'all 0.4s ease',
                    position: 'relative',
                    overflow: 'hidden',
                    cursor: 'pointer',
                    '&::before': {
                      content: '""',
                      position: 'absolute',
                      inset: 0,
                      background: 'linear-gradient(135deg, rgba(139, 92, 246, 0.1), rgba(59, 130, 246, 0.1))',
                      opacity: 0,
                      transition: 'opacity 0.4s ease'
                    },
                    '&:hover': {
                      transform: 'translateY(-6px)',
                      borderColor: 'rgba(139, 92, 246, 0.6)',
                      boxShadow: '0 12px 40px rgba(139, 92, 246, 0.3)',
                      '&::before': {
                        opacity: 1
                      }
                    }
                  }}
                >
                  <Typography 
                    variant="h6" 
                    sx={{ 
                      color: '#e5e7eb',
                      position: 'relative',
                      zIndex: 1,
                      transition: 'all 0.4s ease',
                      '&:hover': {
                        background: 'linear-gradient(135deg, #c4b5fd, #93c5fd)',
                        WebkitBackgroundClip: 'text',
                        WebkitTextFillColor: 'transparent',
                        backgroundClip: 'text'
                      }
                    }} 
                    fontWeight="medium"
                  >
                    {company}
                  </Typography>
                </Paper>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>

      {/* CTA Section */}
      <Box
        sx={{
          background: 'linear-gradient(135deg, rgba(88, 28, 135, 0.9) 0%, rgba(30, 58, 138, 0.9) 100%)',
          color: 'white',
          py: 8,
          textAlign: 'center'
        }}
      >
        <Container maxWidth="lg">
          <Typography variant="h3" gutterBottom>
            Join Our Success Stories
          </Typography>
          <Typography variant="h6" sx={{ mb: 4, opacity: 0.95, color: '#e5e7eb' }}>
            Ready to transform your business with AI-driven solutions?
          </Typography>
        </Container>
      </Box>
    </Box>
  );
};

export default Testimonials;