import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  CardMedia,
  Chip,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions
} from '@mui/material';
import { Launch, Email, WhatsApp } from '@mui/icons-material';
import axios from 'axios';

const Projects = () => {
  const [projects, setProjects] = useState([]);
  const [selectedProject, setSelectedProject] = useState(null);
  const [dialogOpen, setDialogOpen] = useState(false);

  useEffect(() => {
    // Fetch projects
    axios.get('/api/projects')
      .then(response => setProjects(response.data))
      .catch(error => console.error('Error fetching projects:', error));
  }, []);

  const handleProjectClick = (project) => {
    setSelectedProject(project);
    setDialogOpen(true);
  };

  const handleCloseDialog = () => {
    setDialogOpen(false);
    setSelectedProject(null);
  };

  const getIndustryColor = (industry) => {
    const colors = {
      'Retail': 'success',
      'SaaS': 'primary',
      'Finance': 'warning',
      'Healthcare': 'error',
      'Education': 'info'
    };
    return colors[industry] || 'default';
  };

  return (
    <Box sx={{ backgroundColor: '#030303' }}>
      {/* Hero Section */}
      <Box
        sx={{
          background: 'linear-gradient(135deg, rgba(88, 28, 135, 0.9) 0%, rgba(30, 58, 138, 0.9) 100%)',
          color: 'white',
          py: { xs: 7, md: 9 },
          textAlign: 'center',
          borderBottom: '1px solid rgba(255,255,255,0.06)'
        }}
      >
        <Container maxWidth="lg">
          <Typography variant="h2" component="h1" gutterBottom sx={{ fontWeight: 'bold', color: '#ffffff' }}>
            Our Projects
          </Typography>
          <Typography variant="h5" sx={{ opacity: 0.95, color: '#e5e7eb' }}>
            Showcasing successful digital transformations and AI implementations
          </Typography>
        </Container>
      </Box>

      {/* Projects Grid */}
      <Box sx={{ py: 8, backgroundColor: '#050505' }}>
        <Container maxWidth="lg">
          <Typography variant="h3" textAlign="center" gutterBottom sx={{ color: 'white', fontWeight: 'bold' }}>
            Case Studies
          </Typography>
          <Typography variant="h6" textAlign="center" sx={{ mb: 6, opacity: 0.9, color: '#cbd5e1' }}>
            Real results from real projects
          </Typography>

          <Grid container spacing={4}>
            {projects.map((project) => (
              <Grid item xs={12} md={6} lg={4} key={project.id}>
                <Card 
                  sx={{ 
                    height: '100%', 
                    cursor: 'pointer',
                    transition: 'all 0.4s cubic-bezier(0.4, 0, 0.2, 1)',
                    backgroundColor: 'rgba(20,20,30,0.9)',
                    border: '2px solid rgba(139, 92, 246, 0.3)',
                    borderRadius: 3,
                    overflow: 'hidden',
                    position: 'relative',
                    backdropFilter: 'blur(10px)',
                    boxShadow: '0 8px 32px rgba(139, 92, 246, 0.2)',
                    '&::before': {
                      content: '""',
                      position: 'absolute',
                      inset: 0,
                      background: 'linear-gradient(135deg, rgba(139, 92, 246, 0.1), rgba(59, 130, 246, 0.1))',
                      opacity: 0,
                      transition: 'opacity 0.4s ease',
                      zIndex: 0,
                      pointerEvents: 'none'
                    },
                    '&:hover': {
                      transform: 'translateY(-12px) scale(1.02)',
                      boxShadow: '0 20px 60px rgba(139, 92, 246, 0.5), 0 0 80px rgba(59, 130, 246, 0.3)',
                      borderColor: 'rgba(139, 92, 246, 0.8)',
                      '&::before': {
                        opacity: 1
                      }
                    }
                  }}
                  onClick={() => handleProjectClick(project)}
                >
                  <CardMedia
                    sx={{
                      height: 220,
                      background: 'linear-gradient(135deg, #7c3aed 0%, #4f46e5 50%, #3b82f6 100%)',
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      position: 'relative',
                      overflow: 'hidden',
                      boxShadow: 'inset 0 -40px 80px rgba(0,0,0,0.3)',
                      '&::before': {
                        content: '""',
                        position: 'absolute',
                        top: '-100%',
                        left: '-100%',
                        width: '300%',
                        height: '300%',
                        background: 'radial-gradient(circle, rgba(255, 255, 255, 0.15) 0%, transparent 50%)',
                        animation: 'shimmer 8s linear infinite',
                      },
                      '&::after': {
                        content: '""',
                        position: 'absolute',
                        inset: 0,
                        background: 'radial-gradient(circle at 50% 0%, rgba(255, 255, 255, 0.2), transparent 60%)',
                      },
                      '@keyframes shimmer': {
                        '0%': { transform: 'translate(-50%, -50%) rotate(0deg)' },
                        '100%': { transform: 'translate(-50%, -50%) rotate(360deg)' }
                      }
                    }}
                  >
                    <Box 
                      sx={{ 
                        position: 'relative', 
                        zIndex: 2, 
                        textAlign: 'center',
                        animation: 'float 3s ease-in-out infinite',
                        '@keyframes float': {
                          '0%, 100%': { transform: 'translateY(0px)' },
                          '50%': { transform: 'translateY(-10px)' }
                        }
                      }}
                    >
                      <Box
                        sx={{
                          fontSize: '5rem',
                          mb: 1.5,
                          filter: 'drop-shadow(0 4px 30px rgba(255, 255, 255, 0.4)) drop-shadow(0 0 60px rgba(139, 92, 246, 0.8))',
                          transition: 'transform 0.3s ease',
                          '&:hover': {
                            transform: 'scale(1.2) rotate(5deg)'
                          }
                        }}
                      >
                        {project.industry === 'Retail' && ''}
                        {project.industry === 'SaaS' && ''}
                        {project.industry === 'Finance' && ''}
                        {project.industry === 'Healthcare' && ''}
                        {project.industry === 'Education' && ''}
                        {!['Retail', 'SaaS', 'Finance', 'Healthcare', 'Education'].includes(project.industry) && ''}
                      </Box>
                      <Typography 
                        variant="h5" 
                        sx={{ 
                          color: '#ffffff',
                          fontWeight: 800,
                          textShadow: '0 2px 4px rgba(0,0,0,0.3), 0 4px 20px rgba(255,255,255,0.3)',
                          letterSpacing: '0.5px',
                          textTransform: 'uppercase'
                        }}
                      >
                        {project.industry}
                      </Typography>
                    </Box>
                  </CardMedia>
                  <CardContent sx={{ position: 'relative', zIndex: 1 }}>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 2 }}>
                      <Typography 
                        variant="h5" 
                        component="h3" 
                        fontWeight="bold" 
                        sx={{ 
                          color: 'white',
                          fontSize: '1.4rem',
                          background: 'linear-gradient(135deg, #ffffff, #e0d5ff)',
                          WebkitBackgroundClip: 'text',
                          WebkitTextFillColor: 'transparent',
                          backgroundClip: 'text'
                        }}
                      >
                        {project.title}
                      </Typography>
                      <Launch 
                        sx={{ 
                          color: '#a78bfa',
                          filter: 'drop-shadow(0 0 8px rgba(167, 139, 250, 0.6))',
                          transition: 'all 0.3s ease',
                          '&:hover': {
                            color: '#c4b5fd',
                            transform: 'translateX(4px) translateY(-4px)'
                          }
                        }} 
                      />
                    </Box>
                    
                    <Chip 
                      label={project.industry} 
                      color={getIndustryColor(project.industry)}
                      size="small"
                      sx={{ 
                        mb: 2,
                        fontWeight: 700,
                        fontSize: '0.85rem',
                        boxShadow: '0 4px 12px rgba(139, 92, 246, 0.3)'
                      }}
                    />
                    
                    <Typography 
                      variant="body2" 
                      sx={{ 
                        color: '#e5e7eb', 
                        mb: 2.5,
                        lineHeight: 1.7,
                        fontSize: '0.95rem'
                      }}
                    >
                      {project.description}
                    </Typography>
                    
                    <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.8, mb: 2.5 }}>
                      {project.services.map((service, index) => (
                        <Chip
                          key={index}
                          label={service}
                          variant="outlined"
                          size="small"
                          sx={{
                            borderColor: 'rgba(139, 92, 246, 0.4)',
                            color: '#c4b5fd',
                            fontSize: '0.8rem',
                            fontWeight: 500,
                            transition: 'all 0.3s ease',
                            '&:hover': {
                              borderColor: 'rgba(139, 92, 246, 0.8)',
                              backgroundColor: 'rgba(139, 92, 246, 0.1)',
                              transform: 'translateY(-2px)',
                              boxShadow: '0 4px 8px rgba(139, 92, 246, 0.3)'
                            }
                          }}
                        />
                      ))}
                    </Box>
                    
                    <Typography 
                      variant="body2" 
                      fontWeight="bold" 
                      sx={{ 
                        color: '#34d399',
                        fontSize: '1rem',
                        textShadow: '0 0 20px rgba(52, 211, 153, 0.4)',
                        padding: '8px 0',
                        borderTop: '1px solid rgba(139, 92, 246, 0.2)',
                        mt: 1
                      }}
                    >
                      {project.outcome}
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>

          {projects.length === 0 && (
            <Box sx={{ textAlign: 'center', py: 8 }}>
              <Typography variant="h6" sx={{ color: '#9ca3af' }}>
                Loading projects...
              </Typography>
            </Box>
          )}
        </Container>
      </Box>

      {/* Project Details Dialog */}
      <Dialog
        open={dialogOpen}
        onClose={handleCloseDialog}
        maxWidth="md"
        fullWidth
        PaperProps={{
          sx: {
            backgroundColor: '#1a1a1a',
            color: '#ffffff'
          }
        }}
      >
        {selectedProject && (
          <>
            <DialogTitle sx={{ backgroundColor: '#0a0a0a', borderBottom: '2px solid rgba(255, 255, 255, 0.1)' }}>
              <Typography variant="h4" fontWeight="bold" sx={{ color: '#ffffff', mb: 1 }}>
                {selectedProject.title}
              </Typography>
              <Chip 
                label={selectedProject.industry} 
                color={getIndustryColor(selectedProject.industry)}
                sx={{ mt: 1, fontWeight: 'bold' }}
              />
            </DialogTitle>
            <DialogContent sx={{ pt: 3 }}>
              <Box sx={{ mb: 4 }}>
                <Typography variant="h6" gutterBottom sx={{ color: '#e0e0e0', fontWeight: 'bold', fontSize: '1.1rem', mb: 2 }}>
                  Services Provided:
                </Typography>
                <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1.5, mb: 3 }}>
                  {selectedProject.services.map((service, index) => (
                    <Chip
                      key={index}
                      label={service}
                      sx={{ 
                        backgroundColor: 'rgba(88, 28, 135, 0.3)',
                        color: '#b998d9',
                        fontWeight: '600',
                        fontSize: '0.9rem',
                        border: '1px solid rgba(185, 152, 217, 0.4)'
                      }}
                    />
                  ))}
                </Box>
              </Box>

              <Box sx={{ mb: 4 }}>
                <Typography variant="h6" gutterBottom sx={{ color: '#e0e0e0', fontWeight: 'bold', fontSize: '1.1rem', mb: 1.5 }}>
                  Project Description:
                </Typography>
                <Typography variant="body1" paragraph sx={{ color: '#d0d0d0', fontSize: '1rem', lineHeight: '1.6' }}>
                  {selectedProject.description}
                </Typography>
              </Box>

              <Box sx={{ mb: 2 }}>
                <Typography variant="h6" gutterBottom sx={{ color: '#e0e0e0', fontWeight: 'bold', fontSize: '1.1rem', mb: 1.5 }}>
                  Results Achieved:
                </Typography>
                <Typography variant="body1" sx={{ color: '#10b981', fontWeight: 'bold', fontSize: '1.05rem', backgroundColor: 'rgba(16, 185, 129, 0.1)', p: 2, borderRadius: 1, border: '1px solid rgba(16, 185, 129, 0.2)' }}>
                   {selectedProject.outcome}
                </Typography>
              </Box>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleCloseDialog} color="primary">
                Close
              </Button>
            </DialogActions>
          </>
        )}
      </Dialog>

      {/* Industries We Serve */}
      <Box sx={{ py: 8, backgroundColor: '#030303' }}>
        <Container maxWidth="lg">
          <Typography variant="h3" textAlign="center" gutterBottom sx={{ color: 'white', fontWeight: 'bold' }}>
            Industries We Serve
          </Typography>
          <Grid container spacing={3} sx={{ mt: 2 }}>
            {[
              'Technology & SaaS',
              'Finance & FinTech',
              'E-Commerce',
              'Healthcare',
              'Education',
              'Manufacturing'
            ].map((industry, index) => (
              <Grid item xs={12} sm={6} md={4} key={index}>
                <Card sx={{ p: 3, textAlign: 'center', height: '100%', backgroundColor: 'rgba(255,255,255,0.04)', border: '1px solid rgba(255,255,255,0.08)' }}>
                  <Typography variant="h6" fontWeight="bold" sx={{ color: 'white' }}>
                    {industry}
                  </Typography>
                </Card>
              </Grid>
            ))}
          </Grid>
        </Container>
      </Box>

      {/* CTA Section */}
      <Box
        sx={{
          background: 'linear-gradient(135deg, rgba(88, 28, 135, 0.9) 0%, rgba(30, 58, 138, 0.9) 100%)',
          color: 'white',
          py: 8,
          textAlign: 'center'
        }}
      >
        <Container maxWidth="lg">
          <Typography variant="h3" gutterBottom>
            Ready to Start Your Project?
          </Typography>
          <Typography variant="h6" sx={{ mb: 2, opacity: 0.95, color: '#e5e7eb' }}>
            Let's create something amazing together. Contact us to discuss your requirements.
          </Typography>
          <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', gap: 1, mb: 1 }}>
            <Email sx={{ fontSize: '1.3rem' }} />
            <Typography variant="body1" sx={{ fontSize: '1.1rem' }}>
              cognixpvtltb@gmail.com
            </Typography>
          </Box>
          <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', gap: 1, mb: 4 }}>
            <WhatsApp sx={{ fontSize: '1.3rem' }} />
            <Typography variant="body1" sx={{ fontSize: '1.1rem' }}>
              +91 7991955775
            </Typography>
          </Box>
        </Container>
      </Box>
    </Box>
  );
};

export default Projects;